<template>
  <div class="header">
    <div class="title"><img @click="back" src="../assets/images/header/back.png" alt="">{{title}}</div>
  </div>
</template>

<script>
  export default {
    name: 'header',
    props: ['title'],
    methods: {
      back: function() {
        this.$router.go(-1)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/header.scss';
</style>



