<template>
  <div>
    <div class="nav">
      <span @click="lastWeek"><img src="../assets/images/ddjl/left.png" alt=""></span>
      <span>{{dayFormat(begin_time)}}-{{dayFormat(end_time)}}</span>
      <span @click="nextWeek"><img src="../assets/images/ddjl/right.png" alt=""></span>
    </div>
     <div class="list">
      <div class="week">
        <ul>
          <li @click="tabClick(index)" :class="indexday=== index ?'active':''" v-for="(item,index) in list" :key="index">
            <div>
              <span>星期{{item.week}}</span>
              <span>{{item.date}}</span>
            </div>
          </li>
        
        </ul>
      </div>
      <section v-if="list.length>0">
        <ul>
          <li  v-for="(item,index) in list[indexday].datalist" :key="index" >
            <p>
              <span>{{item.book_time}}</span>
              <span>分派护士：{{item.nurse_name}} </span>
            </p>
            <h3>{{item.product_name}}<span>{{item.order_text}}</span></h3>
            <h4>{{item.order_address}}</h4>
          </li>
       
        </ul>
      </section>
    </div>
  </div>
</template>

<script>
import { getVisit } from '../service/getData'
import moment from 'moment'
export default {
  name: 'week',
  data() {
    return {
      date: '',
      week1: '',
      begin_time: '',
      end_time: '',
      activeWeekDay: '',
      moments: moment(),
      indexday:0,
      list: []
    }
  },
  created() {
    this.begin_time = this.weekStart().format('YYYY-MM-DD')
    this.end_time = this.weekEnd().format('YYYY-MM-DD')
  },
  mounted() {
    this.getData()
  },
  methods: {
    weekStart(date) {
      let today = date ? date : moment()
      let daystoMonday = 0 - today.isoWeekday() + 7
      let nextMonday = today.subtract('days', daystoMonday)
      return nextMonday
    },
    weekEnd(date) {
      let today = date ? date : moment()
      let nextMonday = this.weekStart()
      let nextSunday = nextMonday.add('days', 6)
      return nextSunday
    },
    nextWeek() {
      let day = moment(this.begin_time)
      let today = day.weekday(8)
      this.begin_time = today.format('YYYY-MM-DD')
      this.end_time = today.add(6, 'days').format('YYYY-MM-DD')
      this.getData()
    },
    lastWeek() {
      let day = moment(this.begin_time)
      let today = day.weekday(-6)
      this.begin_time = today.format('YYYY-MM-DD')
      this.end_time = today.add(6, 'days').format('YYYY-MM-DD')
      this.getData()
    },
    dayFormat(day) {
      return moment(day).format('MM月DD日')
    },
    getData() {
      let data = {
        token: this.$store.state.userId,
        begin_time: this.begin_time,
        end_time: this.end_time
      }
      getVisit(data).then(res => {
        this.list = res.data.list
        this.indexday = 0
      })
    },
    tabClick(index){
      this.indexday = index
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/week.scss';
</style>



