<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    beforeMount: function() {
      if (localStorage.userId === undefined) {
        this.$router.push({
          name: 'login'
        })
      } else {
        this.$store.state.userId = localStorage.userId
      }
    }
  }
</script>



