import Vue from 'vue'
import Router from 'vue-router'
import myOrderForm from '@/pages/myOrderForm'
import question from '@/pages/question'
import toBeSetOut from '@/pages/toBeSetOut'
import toArrive from '@/pages/toArrive'
import undetermined from '@/pages/undetermined'
import WaitForTheDeparture from '@/pages/WaitForTheDeparture'
import server from '@/pages/server'
import cancel from '@/pages/cancel'
import success from '@/pages/success'
import NursesIndex from '@/pages/nurses/Index'
import NursesIndex0 from '@/pages/nurses/index_0_no_order'
import NursesIndex1 from '@/pages/nurses/index_1_confirm_order'
import NursesIndex2 from '@/pages/nurses/index_2_confirm_depart'
import NursesIndex3 from '@/pages/nurses/index_3_service'
import login from '@/pages/login'
import orderHistory from '@/pages/orderHistory'
import week from '@/pages/week'
import month from '@/pages/month'
import dayList from '@/pages/dayList'
import outCall from '@/pages/outCall'
import outCallWeek from '@/pages/outCallWeek'
import outCallMonth from '@/pages/outCallMonth'
import outCallList from '@/pages/outCallList'
import set from '@/pages/set'
import center from '@/pages/center'
import message from '@/pages/message'
import photo from '@/pages/photo'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/', //护士端  首页
    name: 'nursesindex',
    component: NursesIndex
  }, {
    path: '/nursesindex0', //护士端 首页-0-初始化
    name: 'nursesindex0',
    component: NursesIndex0
  }, {
    path: '/nursesindex1', //护士端  首页-1-确定订单
    name: 'nursesindex1',
    component: NursesIndex1
  }, {
    path: '/nursesindex2', //护士端  首页-2-确定出发
    name: 'nursesindex2',
    component: NursesIndex2
  }, {
    path: '/nursesindex3', //护士端  首页-3-服务中
    name: 'nursesindex3',
    component: NursesIndex3
  }, {
    path: '/myOrderForm/:status',
    name: 'myOrderForm',
    component: myOrderForm //我的订单
  }, {
    path: '/toBeSetOut/:order_no',
    name: 'toBeSetOut',
    component: toBeSetOut //待出发
  }, {
    path: '/toArrive/:order_no',
    name: 'toArrive',
    component: toArrive //已出发
  }, {
    path: '/undetermined/:order_no',
    name: 'undetermined',
    component: undetermined //待确定
  }, {
    path: '/WaitForTheDeparture',
    name: 'WaitForTheDeparture',
    component: WaitForTheDeparture //等待出发
  }, {
    path: '/server/:order_no',
    name: 'server',
    component: server //服务中
  }, {
    path: '/cancel',
    name: 'cancel',
    component: cancel //取消订单
  }, {
    path: '/success/:order_no',
    name: 'success',
    component: success //服务完成
  }, {
    path: '/login', //登陆
    name: 'login',
    component: login
  }, {
    path: '/orderHistory', //订单记录
    component: orderHistory,
    children: [{
      path: '/',
      name: 'orderHistoryWeek',
      component: week,
    }, {
      path: '/month',
      name: 'month',
      component: month,
    }]
  }, {
    path: '/dayList/:id', //订单记录-单日
    name: 'dayList',
    component: dayList
  }, {
    path: '/outCall', //出诊记录
    component: outCall,
    children: [{
      path: '/',
      name: 'outCallWeek',
      component: outCallWeek,
    }, {
      path: '/outCallMonth',
      name: 'outCallMonth',
      component: outCallMonth,
    }]
  }, {
    path: '/outCallList/:id',    //出诊记录-单日
    name: 'outCallList',
    component: outCallList,
  }, {
    path: '/set',//设置
    name: 'set',
    component: set,
  }, {
    path: '/center',//个人中心
    name: 'center',
    component: center,
  }, {
    path: '/message',//消息
    name: 'message',
    component: message,
  }, {
    path: '/photo/:base64',//修改图片
    name: 'photo',
    component: photo,
  }]
})



