<template>
  <div class="set">
    <div class="title">
      <img @click="back" src="../assets/images/header/back.png" alt="">
      <h2>设置</h2>
      <span @click="edit">完成</span>
    </div>
    <div class="user">
    <router-link to="/photo/123">  <section class="header">
        <label for="">头像</label>
        <!-- <div><img :src="data" alt=""></div> -->
       <div><img :src="userMsg.head_pic" alt=""></div> 
        <!-- <form id="form1" method="POST">
          <input @change="images($event)" type="file" name="image" accept=".gif,.jpg,.jpeg,.png">
        </form> -->
      </section></router-link>
      <section class="name">
        <label for="">姓名</label>
        <input @focus="focusName" type="text" name="" v-model="userMsg.realname">
      </section>
      <section class="name">
        <label for="">年龄</label>
        <input @focus="focusAge" type="text" name="" v-model="userMsg.age">
      </section>
      <section class="sex" @click="sexChange">
        <label for="">性别</label>
        <span v-if="userMsg.gender === 1">男</span>
        <span v-else>女</span>
      </section>
    </div>
    <div class="msg">
      <section class="name">
        <label for="">联系方式</label>
        <input @focus="focusTel" @blur="blurTel" type="text" name="" v-model="userMsg.phone">
      </section>
      <section class="name">
        <label for="">身份证号</label>
        <input @focus="focusId" type="text" name="" v-model="userMsg.id_card">
      </section>
    </div>
    <div class="btn" @click="login">退出登录</div>
    <van-popup v-model="show" position="bottom" :close-on-click-overlay="false">
      <section class="changeSex">
        <p><span @click="sexCancel">取消</span><span @click="end">确认</span></p>
        <ul>
          <li></li>
          <li :class="userMsg.gender === 1 ? 'active' : ''" @click="sexActive(1)">男</li>
          <li :class="userMsg.gender === 2 ? 'active' : ''" @click="sexActive(2)">女</li>
        </ul>
      </section>
    </van-popup>
  </div>
</template>

<script>
import { nurseCenter, nurseEdit, fileUpload } from '../service/getData'
import { mapActions } from 'vuex'
import Vue from 'vue'
import $ from 'jquery'
import { Toast } from 'vant'
export default {
  name: 'set',
  data() {
    return {
      userMsg: '',
      show: false,
      oddSex: '',
      imageUrl: '',
      data: 'http://www.zpdyht.com/hlylAssets/uploads/2018-01-14/e9aba8c67b20d0d95afc76390f802364.jpg'
    }
  },
  mounted: function() {
    nurseCenter(this.$store.state.userId).then(res => {
      this.userMsg = res.data
    })
  },
  methods: {
    ...mapActions([
      // 从store上绑定的getters中载入需要的到此组件中
      'actionUserId'
    ]),
    back: function() {
      this.$router.go(-1)
    },
    login: function() {
      this.actionUserId('')
      this.$router.push({
        name: 'login'
      })
    },
    sexChange: function() {
      this.oddSex = this.userMsg.gender
      this.show = true
    },
    sexCancel: function() {
      this.userMsg.gender = this.oddSex
      this.show = false
    },
    sexActive: function(sex) {
      this.userMsg.gender = sex
    },
    end: function() {
      this.show = false
    },
    edit: function() {
      nurseEdit(
        this.userMsg.realname,
        this.userMsg.age,
        this.userMsg.gender,
        this.userMsg.phone,
        this.userMsg.id_card,
        this.$store.state.userId
      ).then(res => {
        this.$router.push({
          name: 'center'
        })
      })
    },
    focusName: function() {
      let name = this.userMsg.realname
      this.userMsg.realname = ''
      Vue.nextTick(() => {
        this.userMsg.realname = name
      })
    },
    focusAge: function() {
      let age = this.userMsg.age
      this.userMsg.age = ''
      Vue.nextTick(() => {
        this.userMsg.age = age
      })
    },
    focusTel: function() {
      let tel = this.userMsg.phone
      this.userMsg.phone = ''
      Vue.nextTick(() => {
        this.userMsg.phone = tel
      })
    },
    focusId: function() {
      let id = this.userMsg.id_card
      this.userMsg.id_card = ''
      Vue.nextTick(() => {
        this.userMsg.id_card = id
      })
    },
    blurTel() {
      if (!/^[0-9]{11}$/.test(this.userMsg.phone)) {
        Toast('请输入正确手机号')
        this.userMsg.phone = ''
      }
    },
    images(e) {
      var reader = new FileReader()
      var that = this
      let file = e.target.files[0]
      reader.readAsDataURL(file)
      reader.onload = function(e) {
        that.$router.push({
          name: 'photo',
          params: {
            base64: this.result
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/set.scss';
</style>



