<template>
  <div class="page-nurses">
    <div class="page-content-top">

        <!-- 头部个人信息 start -->
        <div class="page-head">

            <!-- 头部个人头像及工具栏 start -->
            <div class="self-info">
                <div class="self-photo" @click="center">
                    <img v-bind:src="myInfo.head" alt="">
                </div>
                <div class="self-toolbar">
                    <ul>
                        <li><router-link to="/orderHistory" class="icon icon-005"></router-link></li>
                        <li @click="message"><a href="javascript:void(0);" class="icon icon-006"></a></li>
                    </ul>
                </div>
            </div>
            <!-- 头部个人头像及工具栏 end -->

            <!-- 头部订单信息 start -->
            <div class="self-order">
                <div class="self-order-createtime">
                    <p>{{order.bookTimeText}}</p>
                    <p>最近订单开始时间</p>
                </div>
                <div class="self-order-num">
                    <p>{{order.today_count}}</p>
                    <p>今日订单数</p>
                </div>
            </div>
            <!-- 头部订单信息 end -->

        </div>
        <!-- 头部个人信息 end -->

        <!-- 页面主体内容 start -->
        <div class="page-body">

            <!-- 仪表盘图表 start -->
            <div class="gauge-chart-box">

                <!-- 订单地址及操作按钮 start -->
                <div class="opts">
                    <p class="order-address">{{order.address}}</p>
                    <button v-bind:class="btnClass" v-if="![0,2].includes(order.status)" @click.async="btnClick">{{order.buttonText}}</button>
                </div>
                <!-- 订单地址及操作按钮 end -->

                <div class="gauge-chart"></div>
            </div>
            <!-- 仪表盘图表 end -->

        </div>
        <!-- 页面主体内容 end -->

    </div>

    <!-- 页面底部菜单 start -->
    <div class="page-bottom">
        <ul class="clearfix">
            <li class="bbline">
                <div class="item brline" @click="myOrderForm(5)">
                    <div class="nav-num colored-nav">{{order.receiveTotal}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-confirm"></i>
                        <span class="nav-title-text">待确定订单</span>
                    </div>
                </div>
                <div class="item" @click="myOrderForm(7)">
                    <div class="nav-num colored-nav">{{order.startTotal}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-wait"></i>
                        <span class="nav-title-text">待出诊订单</span>
                    </div>
                </div>
            </li>
            <li>
                <div class="item brline" @click="myOrderForm(11)">
                    <div class="nav-num colored-nav">{{order.servingTotal}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-service"></i>
                        <span class="nav-title-text">服务中订单</span>
                    </div>
                </div>
                <div class="item" @click="myOrderForm('全部')">
                    <!--<div class="nav-num colored-nav"></div>-->
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-total"></i>
                        <span class="nav-title-text">全部订单</span>
                    </div>
                </div>
            </li>
        </ul>
    </div>
    <!-- 页面底部菜单 end -->

  </div>
</template>

<script>
  export default {
    name: "NursesIndex",
    data: function() {
      return {

        // 订单操作
        ORDEROPERATION: {
          RECEIVE: 0, // 确定订单
          START: 1,   // 确定出发
          ARRIVE: 2   // 确定到达
        },

        // 订单界面状态
        // 这里定义的状态与业务状态无关，仅做界面标识用途
        ORDERSTATUS: {
          NONE: 0,         // 没有订单-等待接单-图表15分钟（无动效）
          RECEIVE: 1,      // 订单来了，但还没有受理订单-对应下一操作0-图表15分钟（有动效）
          BEFORESTART: 2,  // 出诊之前，还没有提示出发-图表50分钟（无动效）
          START: 3,        // 提示出发-对应下一操作1-图表50分钟（有动效）
          TRAVELING: 4,    // 出发了，在行程中-对应下一操作2-图表60分钟（有动效）
          SERVICING: 5,    // 在服务中-图表60分钟（有动效）
          FINISH: 6        // 完成-图表（先做60分钟）（无动效）
        },

        // 订单业务状态
        // 这里定义的状态为首页需要的业务状态，与业务状态一一对应
        BUSINESSSTATUS: {
          RECEIVE: 5,      // 护士待确定(客服派单)
          START: 7,        // 待出发（护士确认订单）
          TRAVELING: 9,    // 已出发（护士确认出发）
          SERVICING: 11,   // 服务中（用户确认完成）
          FINISH: 13       // 服务完成（用户确定完成）
        },

        // 个人信息
        myInfo: {
          head: ""
        },

        // 订单信息
        order: {}
      }
    },
    computed: {
      btnClass: function () {
        return {
          "btn": true,
          "btn-face-1": [1].includes(this.order.status),
          "btn-face-2": [3, 4].includes(this.order.status),
          "btn-face-3": [5, 6].includes(this.order.status)
        }
      }
    },
    filters: {
      num2date: function (value) {
        var that = this;

        if (!value) return "00:00";

        var addZero = function (n) {
          var str = "" + n;
          if (str.length == 1) {
            str = "0" + str;
          }
          return str;
        };

        var date = new Date(Number(value));
        return addZero(date.getMonth() + 1) + "-" + addZero(date.getDate())
               + " " + addZero(date.getHours()) + ":" + addZero(date.getMinutes());
      }
    },
    mounted: function() {
      if (this.$store.state.userId === ''){
        this.$router.push({
          name: 'login'
        })
      }
      this.$nextTick(function () {
        this.chartControl = this.guageControl().init();
        this.nurseCenter();
        this.updateHomepage();
      });
    },
    methods: {

      outCall: function () {
        this.removeInterval();
        this.$router.push({
          name: 'orderHistoryWeek'
        })
      },

      myOrderForm: function (status) {
        this.removeInterval();
        this.$router.push({
          name: 'myOrderForm',
          params: {
            status: status
          }
        })
      },

      // 获取护士个人信息
      nurseCenter: function () {
        var that = this;

        fetch("http://www.zpdyht.com/hlylapi/v1/nurse/nurseCenter?token=" + that.$store.state.userId, {
          method: "GET",
          mode: "cors",
          cache: "default"
        }).then(function (res) {
          res.json().then(function (json) {
            if (json.code == 200) {
              that.myInfo = that.dataAdapter(0, json);
            } else {
              alert(json.msg);
            }
          });
        });
      },

      // 添加定时器
      addInterval: function () {
        var that = this;

        that.removeInterval();

        switch (that.order.status) {
          case 0:
            // 无订单，无定时任务
            break;
          case 1:
          case 3:
          case 4:
          case 5:
            that.interval = setInterval(function () {
              var _option = that.chartControl.getOption();

              // 显示
              var _data1 = _option.series[4].data[0].value;
              var msec1 = that.min2msec(_data1);
              msec1 += 1000;
              var min = that.msec2min(msec1);
              _option.series[4].data[0].value = min;

              // 动效
              var per = 0.4 / 60;
              var _data2 = _option.series[1].data[0].value;
              var msec2 = _data2;
              msec2 += 0.01 + per;
              var min2 = (msec2 > that.order.duration) ? that.order.duration : msec2;
              _option.series[1].data[0].value = min2;
              _option.series[1].data[1].value = that.order.duration - min2;

              that.chartControl.setData(_option, that.order.status, that.order.duration, true);
            }, 1000);
            break;
          case 2:
            that.timeout = setTimeout(function () {
              that.order.status = 3;
              that.order.duration = 50;
              that.order.passTimeMsec = 0;
              that.order.passTimeMin = 0;
              that.order.buttonText = "确认出发";
              that.btnClick = that.confirmStart;
              that.chartControl.setData(that.dataAdapter(2, {data:{}}), that.order.status, that.order.duration, false);
              that.addInterval();
            }, that.order.passTimeMsec);
            break;
          case 6:
            // 5分钟超时不需要处理，刷新页面
            break;
        }
      },

      // 移除定时器
      removeInterval: function () {
        var that = this;

        if (that.interval) {
          clearInterval(that.interval);
          that.interval = null;
        }
        if (that.timeout) {
          clearTimeout(that.timeout);
          that.timeout = null;
        }
      },

      // 更新首页数据（对应接口-获取护士首页数据）
      updateHomepage: function () {
        var that = this;

        fetch("http://www.zpdyht.com/hlylapi/v1/order/getHomeData?token=" + that.$store.state.userId + "&_t=" + (new Date().getTime()), {
          method: "GET",
          mode: "cors",
          cache: "no-cache"
        }).then(function (res) {
          res.json().then(function (json) {
            if (json.code == 200) {
              that.order = that.dataAdapter(1, json);
              that.chartControl.setData(that.dataAdapter(2, json), that.order.status, that.order.duration, false, {
                orderText: that.order.orderText || "暂无订单"
              });
              that.addInterval();
            } else {
              alert(json.msg);
            }
          });
        });
      },

      // 操作：确定订单
      confirmJiedan: function () {
        var that = this;

        fetch("http://www.zpdyht.com/hlylapi/v1/order/confirmJiedan", {
          method: "POST",
          mode: "cors",
          cache: "default",
          headers: {
              "Content-Type": "application/json"
          },
          body: JSON.stringify({
            "token": that.$store.state.userId,
            "orderId": that.order.orderId
          })
        }).then(function (res) {
          res.json().then(function (json) {
            if (json.code == 200) {
              that.updateHomepage();
            } else {
              alert(json.msg);
            }
          });
        });
      },

      // 操作：确定出发
      confirmStart: function () {
        var that = this;

        fetch("http://www.zpdyht.com/hlylapi/v1//order/confirmStart", {
          method: "POST",
          mode: "cors",
          cache: "default",
          headers: {
              "Content-Type": "application/json"
          },
          body: JSON.stringify({
            "token": that.$store.state.userId,
            "orderId": that.order.orderId
          })
        }).then(function (res) {
          res.json().then(function (json) {
            if (json.code == 200) {
              that.updateHomepage();
            } else {
              alert(json.msg);
            }
          });
        });
      },

      // 操作：确定到达
      confirmArrive: function () {
        var that = this;

        fetch("http://www.zpdyht.com/hlylapi/v1/order/confirmArrive", {
          method: "POST",
          mode: "cors",
          cache: "default",
          headers: {
              "Content-Type": "application/json"
          },
          body: JSON.stringify({
            "token": that.$store.state.userId,
            "orderId": that.order.orderId
          })
        }).then(function (res) {
          res.json().then(function (json) {
            if (json.code == 200) {
              that.updateHomepage();
            }
          });
        });
      },

      // 计算时间差
      // type: min|msec 分钟|毫秒
      getTimediff: function (type, start, end) {
        var that = this;

        var diffNum = end - start;
        if (type == "min") {
          return that.msec2min(diffNum);
        }
        return diffNum;
      },

      // 分钟转毫秒
      min2msec: function (min) {
        var s = "" + min,
            sarr = s.split(".");
        var n1 = parseInt(sarr[0], 10);
        return Number(n1*60*1000 + (min - n1)*100*1000);
      },

      // 毫秒转分钟
      msec2min: function (msec) {
        var seconds = msec / 1000;
        return Number((Math.floor(seconds / 60) + (seconds % 60) / 100).toFixed(2));
      },

      // 后端与前端数据对接适配
      // type: 0 表示护士个人信息数据，这里只需要头像信息
      // type: 1 表示有关订单信息
      // type: 3 表示图表信息
      dataAdapter: function (type, data) {
        var that = this;

        if (typeof data == "undefined" || typeof data.data == "undefined") return null;

        var result = {};
        data = data.data;
        switch (type) {
          case 0:
              result.head = data.head_pic;
            break;
          case 1: {

              // 时间戳变为毫秒
              data.book_time = data.book_time && data.book_time*1000;
              data.paidan_time = data.paidan_time && data.paidan_time*1000;
              data.cur_time = data.cur_time && data.cur_time*1000;
              if (data.visit) {
                data.visit.order_time = data.visit.order_time && data.visit.order_time * 1000;
                data.visit.start_time = data.visit.start_time && data.visit.start_time * 1000;
                data.visit.arrive_time = data.visit.order_time && data.visit.arrive_time * 1000;
                data.visit.finish_time = data.visit.finish_time && data.visit.finish_time * 1000;
              }

              // 是否有订单
              result.hasOrder = data.today_count > 0 ? true : false;

              // 订单ID
              result.orderId = data.id;

              // 订单地址
              result.address = data.order_address;

              // 订单相对时间-（以服务器时间计算）
              result.curTime = data.cur_time;

              // 订单派单时间
              result.paidanTime = data.paidan_time;

              // 最近一单预约时间
              result.bookTime = data.book_time || 0;
              result.bookTimeText = data.book_time_text || "00:00";

              // 订单时间
              // 订单服务时间
              result.orderTime = result.bookTime;

              // 订单应该出发时间-提前1小时
              result.startTime = result.orderTime - 1 * 60 * 60 * 1000;

              // 按钮文本
              result.buttonText = data.button_text;

              // 订单文本
              result.orderText = data.order_text;

              // 订单业务状态
              result.businessStatus = data.status;

              switch (result.businessStatus) {
                case that.BUSINESSSTATUS.RECEIVE:
                  result.status = that.ORDERSTATUS.RECEIVE;
                  result.duration = 15;
                  result.passTimeMsec = that.getTimediff("msec", result.paidanTime, result.curTime);
                  result.passTimeMin = that.getTimediff("min", result.paidanTime, result.curTime);
                  that.btnClick = that.confirmJiedan;
                  break;
                case that.BUSINESSSTATUS.START:
                  result.duration = 50;
                  if (result.curTime < result.startTime) {
                    result.status = that.ORDERSTATUS.BEFORESTART;
                    result.passTimeMsec = that.getTimediff("msec", result.curTime, result.startTime) - 1*60*1000;
                    that.btnClick = function(){};
                  } else {
                    result.status = that.ORDERSTATUS.START;
                    result.passTimeMsec = that.getTimediff("msec", result.startTime, result.curTime);
                    result.passTimeMin = that.getTimediff("min", result.startTime, result.curTime);
                    that.btnClick = that.confirmStart;
                  }
                  break;
                case that.BUSINESSSTATUS.TRAVELING:
                  result.status = that.ORDERSTATUS.TRAVELING;
                  result.duration = 60;
                  result.passTimeMsec = that.getTimediff("msec", data.visit.start_time, result.curTime);
                  result.passTimeMin = that.getTimediff("min", data.visit.start_time, result.curTime);
                  that.btnClick = that.confirmArrive;
                  break;
                case that.BUSINESSSTATUS.SERVICING:
                  result.status = that.ORDERSTATUS.SERVICING;
                  result.duration = 60;
                  result.passTimeMsec = that.getTimediff("msec", data.visit.arrive_time, result.curTime);
                  result.passTimeMin = that.getTimediff("min", data.visit.arrive_time, result.curTime);
                  result.buttonText = result.orderText;
                  that.btnClick = function(){};
                  break;
                case that.BUSINESSSTATUS.FINISH:
                  result.status = that.ORDERSTATUS.FINISH;
                  result.duration = 60;
                  result.passTimeMsec = that.getTimediff("msec", data.visit.arrive_time, data.visit.finish_time);
                  result.passTimeMin = that.getTimediff("min", data.visit.arrive_time, data.visit.finish_time);
                  result.buttonText = result.orderText;
                  that.btnClick = function(){};
                  break;
                default:
                  result.status = that.ORDERSTATUS.NONE;
                  result.duration = 15;
                  result.address = "";
                  that.btnClick = function(){};
              }

              // 产品名称
              result.productName = data.product_name || "";

              // 今日订单数
              result.today_count = data.today_count;

              // 待确定订单数
              result.receiveTotal = data.receiveTotal;

              // 待出诊订单数
              result.startTotal = data.startTotal;

              // 服务中订单数
              result.servingTotal = data.servingTotal;
            }
            break;
          case 2: {
              switch (that.order.status) {
                case 0:
                case 2:
                  result.series = [
                      {}, {
                          data: [
                              {value: 0},
                              {value: 0},
                              {value: 33.3333333333}
                          ]
                      }, {}, {}, {
                          data: [
                              {
                                name: that.order.productName,
                                value: 0
                              }
                          ]
                      }
                  ];
                  break;
                case 1:
                case 3:
                case 4:
                case 5:
                case 6:
                  var n = parseFloat(that.order.passTimeMin);
                  var per = 0.4 / 60;
                  var pieces = function (n) {
                    var s = "" + n;
                    var sarr = s.split(".");
                    var n1 = parseInt(sarr[0], 10);
                    var n2 = (n - n1) * 100;
                    return n2;
                  }(n);
                  var n2 = n + pieces * per;
                  n2 = n2 > that.order.duration ? that.order.duration : n2;
                  result.series = [
                      {},{
                          data: [
                              {value: n2},
                              {value: that.order.duration - n2},
                              {value: that.order.duration / 2}
                          ]
                      }, {}, {},{
                          data: [
                              {
                                name: that.order.productName,
                                value: n
                              }
                          ]
                      }
                  ];
                  break;
              }
            }
            break;
        }
        return result;
      },

      // 获取图表控制器
      guageControl: (function() {

        // 判断是否为数组
        var isArray = function(o) {
          return Object.prototype.toString.call(o) == "[object Array]";
        };

        // 判断是否为对象
        var isObject = function(o) {
          return Object.prototype.toString.call(o) == "[object Object]";
        };

        // 深度拷贝对象实现
        var extend = (function() {

          var _extend = function(target, source) {

            if (isArray(source)) {
              for (var i = 0; i < source.length; i++) {
                if (Array.isArray(source[i])) {
                  if (typeof target[i] == "undefined" || !isArray(target[i])) {
                    target[i] = [];
                  }
                  _extend(target[i], source[i]);
                } else if (isObject(source[i])) {
                  if (typeof target[i] == "undefined" || !isObject(target[i])) {
                    target[i] = {};
                  }
                  _extend(target[i], source[i]);
                } else {
                  target[i] = source[i];
                }
              }
            } else if (isObject(source)) {
              for (var property in source) {
                if (isArray(source[property])) {
                  if (typeof target[property] == "undefined" || !isArray(target[property])) {
                    target[property] = [];
                  }
                  _extend(target[property], source[property]);
                } else if (isObject(source[property])) {
                  if (typeof target[property] == "undefined" || !isObject(target[property])) {
                    target[property] = {};
                  }
                  _extend(target[property], source[property]);
                } else {
                  target[property] = source[property];
                }
              }
            } else {
              target = source;
            }
          };

          return function() {
            var i = 1,
              len = arguments.length,
              target = arguments[0],
              arg;

            if (!target) return {};

            if (len == 1) {
              target = _extend({}, target);
            } else {
              for (; i < len; i++) {
                arg = arguments[i];
                _extend(target, arg);
              }
            }

            return target;
          }
        })();

        var echarts = require("echarts"),
          gauge = null,
          chart = null;

        // 自适应获取参数
        var getChartAdjustParam = function(type) {
          var currentFontSize = parseFloat(document.documentElement.style.fontSize);

          return {
            sPie1: {
              radius: (162 / 75) * currentFontSize,
              borderWidth: (5 / 75) * currentFontSize
            },
            sPie2: {
              radius: (166 / 75) * currentFontSize
            },
            sPie3: {
              radius: (166 / 75) * currentFontSize
            },
            lPie1: {
              radius: (189 / 75) * currentFontSize
            },
            lPie2: {
              radius: (189 / 75) * currentFontSize
            },
            lPie3: {
              radius: (189 / 75) * currentFontSize
            },
            sGauge: {
              radius: (209 / 75) * currentFontSize,
              axisTick: {
                length: (9 / 75) * currentFontSize
              }
            },
            lGauge: {
              radius: (225 / 75) * currentFontSize,
              splitLine: {
                length: (27 / 75) * currentFontSize
              },
              title: {
                fontSize: (34 / 75) * currentFontSize,
                offsetCenterY: -(74 / 75) * currentFontSize
              },
              detail: {
                time: {
                  fontSize: (82 / 75) * currentFontSize
                },
                unit: {
                  fontSize: (28 / 75) * currentFontSize,
                  lineHeight: (46 / 75) * currentFontSize
                },
                offsetCenterY: (35 / 75) * currentFontSize
              }
            },
            graphic: {
              width: (225 * 2 / 75) * currentFontSize,
              height: (225 * 2 / 75) * currentFontSize,
              fontSize: (18 / 75) * currentFontSize,
              ts: (function() {
                var gaugeWidth = gauge.offsetWidth,
                  gaugeHeight = gauge.offsetHeight;
                var coordX = 0,
                  coordY = 0;
                var r = (225 / 75) * currentFontSize;

                var o = null;
                switch (type) {
                  case 0:
                  case 1:
                    o = {
                      t1: function() {
                        var deg = 30 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 38;
                        var y = r * Math.sin(deg),
                          y = y - 16.5;
                        return {
                          bottom: y,
                          left: x
                        }
                      }(),
                      t2: function() {
                        var deg = 50 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r + x - 8.5;
                        var y = r * Math.sin(deg),
                          y = r + y + 10.5;
                        return {
                          bottom: y,
                          left: x
                        }
                      }(),
                      t3: {
                        invisible: true
                      },
                      t4: {
                        invisible: true
                      },
                      t5: {
                        invisible: true
                      },
                      t6: {
                        invisible: true
                      },
                      t7: {
                        invisible: true
                      },
                      t8: function() {
                        var deg = 50 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 24;
                        var y = r * Math.sin(deg),
                          y = r - y - 21.5;
                        return {
                          top: y,
                          left: x
                        }
                      }(),
                      t9: function() {
                        var deg = 30 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 47;
                        var y = r * Math.sin(deg),
                          y = y - 15.5;
                        return {
                          bottom: y,
                          right: x
                        }
                      }()
                    }
                    break;
                  case 2:
                  case 3:
                    o = {
                      t1: function() {
                        var deg = 30 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 38;
                        var y = r * Math.sin(deg),
                          y = y - 16.5;
                        return {
                          bottom: y,
                          left: x
                        }
                      }(),
                      t2: function() {
                        var deg = 18 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 46;
                        var y = r * Math.sin(deg),
                          y = r + y - 2.5;
                        return {
                          bottom: y,
                          left: x
                        }
                      }(),
                      t3: function() {
                        var deg = 66 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 25;
                        var y = r * Math.sin(deg),
                          y = r - y - 18.5;
                        return {
                          top: y,
                          left: x
                        }
                      }(),
                      t4: function() {
                        var deg = 66 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 25;
                        var y = r * Math.sin(deg),
                          y = r - y - 18.5;
                        return {
                          top: y,
                          right: x
                        }
                      }(),
                      t5: function() {
                        var deg = 18 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 44;
                        var y = r * Math.sin(deg),
                          y = r - y - 13;
                        return {
                          top: y,
                          right: x
                        }
                      }(),
                      t6: function() {
                        var deg = 30 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 47;
                        var y = r * Math.sin(deg),
                          y = y - 15.5;
                        return {
                          bottom: y,
                          right: x
                        }
                      }(),
                      t7: {
                        invisible: true
                      },
                      t8: {
                        invisible: true
                      },
                      t9: {
                        invisible: true
                      }
                    }
                    break;
                  case 4:
                  case 5:
                  case 6:
                    o = {
                      t1: function() {
                        var deg = 30 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 38;
                        var y = r * Math.sin(deg),
                          y = y - 16.5;
                        return {
                          bottom: y,
                          left: x
                        }
                      }(),
                      t2: function() {
                        var deg = 10 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 45;
                        var y = r * Math.sin(deg),
                          y = r + y - 3.5;
                        return {
                          bottom: y,
                          left: x
                        }
                      }(),
                      t3: function() {
                        var deg = 50 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 29;
                        var y = r * Math.sin(deg),
                          y = r - y - 19.5;
                        return {
                          top: y,
                          left: x
                        }
                      }(),
                      t4: function() {
                        var x = r - 15;
                        var y = 0 - 13 - 6;
                        return {
                          top: y,
                          right: x
                        }
                      }(),
                      t5: function() {
                        var deg = 50 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 29;
                        var y = r * Math.sin(deg),
                          y = r - y - 19.5;
                        return {
                          top: y,
                          right: x
                        }
                      }(),
                      t6: function() {
                        var deg = 10 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 45;
                        var y = r * Math.sin(deg),
                          y = r + y - 3.5;
                        return {
                          bottom: y,
                          right: x
                        }
                      }(),
                      t7: function() {
                        var deg = 30 * Math.PI / 180;
                        var x = r * Math.cos(deg),
                          x = r - x - 47;
                        var y = r * Math.sin(deg),
                          y = y - 15.5;
                        return {
                          bottom: y,
                          right: x
                        }
                      }(),
                      t8: {
                        invisible: true
                      },
                      t9: {
                        invisible: true
                      }
                    }
                    break;
                }

                o.circle = {
                  cx: 100,
                  cy: 1,
                  r: (160 / 75) * currentFontSize
                };

                return o;
              })()
            }
          }
        };

        // 自适应到饼图格式
        var getAdapterAdjustParam = function(type) {
          var param = getChartAdjustParam(type);
          return {
            graphic: [{
              width: param.graphic.width,
              height: param.graphic.height,
              children: [{
                  invisible: param.graphic.ts.t1.invisible,
                  bottom: param.graphic.ts.t1.bottom,
                  left: param.graphic.ts.t1.left,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t2.invisible,
                  bottom: param.graphic.ts.t2.bottom,
                  left: param.graphic.ts.t2.left,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t3.invisible,
                  top: param.graphic.ts.t3.top,
                  left: param.graphic.ts.t3.left,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t4.invisible,
                  top: param.graphic.ts.t4.top,
                  right: param.graphic.ts.t4.right,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t5.invisible,
                  top: param.graphic.ts.t5.top,
                  right: param.graphic.ts.t5.right,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t6.invisible,
                  bottom: param.graphic.ts.t6.bottom,
                  right: param.graphic.ts.t6.right,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t7.invisible,
                  bottom: param.graphic.ts.t7.bottom,
                  right: param.graphic.ts.t7.right,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t8.invisible,
                  top: param.graphic.ts.t8.top,
                  left: param.graphic.ts.t8.left,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  invisible: param.graphic.ts.t9.invisible,
                  bottom: param.graphic.ts.t9.bottom,
                  right: param.graphic.ts.t9.right,
                  style: {
                    fontSize: param.graphic.fontSize
                  }
                },
                {
                  shape: {
                    cx: param.graphic.ts.circle.cx,
                    cy: param.graphic.ts.circle.cy,
                    r: param.graphic.ts.circle.r
                  }
                }
              ]
            }],
            series: [{
                radius: [param.sPie1.radius, param.lPie1.radius],
                itemStyle: {
                  normal: {
                    borderWidth: param.sPie1.borderWidth
                  }
                }
              },
              {
                radius: [param.sPie2.radius, param.lPie2.radius]
              },
              {
                radius: [param.sPie3.radius, param.lPie3.radius]
              },
              {
                radius: param.sGauge.radius,
                axisTick: {
                  length: param.sGauge.axisTick.length
                }
              },
              {
                radius: param.lGauge.radius,
                splitLine: {
                  length: param.lGauge.splitLine.length
                },
                title: {
                  fontSize: param.lGauge.title.fontSize,
                  offsetCenter: [0, param.lGauge.title.offsetCenterY]
                },
                detail: {
                  rich: {
                    time: {
                      fontSize: param.lGauge.detail.time.fontSize
                    },
                    unit: {
                      fontSize: param.lGauge.detail.unit.fontSize,
                      lineHeight: param.lGauge.detail.unit.lineHeight
                    }
                  },
                  offsetCenter: [0, param.lGauge.detail.offsetCenterY]
                }
              }
            ]
          }
        };

        // 自定义配置
        var getCustomConfig = function(type, total) {
          var param = {
            series: [{}, {}, {}, {}, {}]
          };

          // 分割段数设置
          var splitNumber = 3;
          if (total > 15 && total <= 50) {
            splitNumber = 5;
          } else if (total > 50 && total <= 60) {
            splitNumber = 6;
          }
          param.series[3].splitNumber = splitNumber;
          param.series[4].splitNumber = splitNumber;

          return param;
        };

        // 仪表盘配置
        var chartOption = {
          title: {
            show: false
          },
          tooltip: {
            show: false
          },
          legend: {
            show: false
          },
          graphic: [{
            type: "group",
            width: 0,
            height: 0,
            bounding: "raw",
            z: -1,
            top: "middle",
            left: "center",
            children: [{
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "0min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "10min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "20min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "30min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "40min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "50min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "60min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "5min"
                }
              },
              {
                type: "text",
                z: 2,
                style: {
                  fill: "#fff",
                  text: "15min"
                }
              },
              {
                type: "circle",
                z: 1,
                left: "center",
                top: "center",
                shape: {
                  cx: 0,
                  cy: 0,
                  r: 0
                },
                style: {
                  fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                      offset: 0,
                      color: "rgba(137,119,246,1)"
                    },
                    {
                      offset: 0.8,
                      color: "rgba(137,119,246,0)"
                    }
                  ])
                }
              }
            ]
          }],
          series: [{
              type: "pie",
              name: "圆环-底色-静止（不用更新）",
              hoverAnimation: false,
              center: ["50%", "50%"],
              radius: [0, 0],
              animation: false,
              z: 5,
              slient: true,
              startAngle: 210,
              label: {
                normal: { show: false },
                emphasis: { show: false }
              },
              labelLine: {
                normal: { show: false },
                emphasis: { show: false }
              },
              itemStyle: {
                normal: {
                  shadowBlur: 30,
                  shadowColor: "rgba(0, 0, 0, 0.15)",
                  shadowOffsetY: 8,
                  borderWidth: 0,
                  borderColor: "rgba(167,156,234,0.9)"
                }
              },
              data: [{
                  name: "剩余时间",
                  value: 66.6666666667,
                  itemStyle: {
                    normal: {
                      color: "rgba(0,0,0,0)"
                    }
                  }
                },
                {
                  name: "时间缺口",
                  value: 33.3333333333,
                  itemStyle: {
                    normal: {
                      color: "rgba(0,0,0,0)",
                      borderWidth: 0
                    }
                  }
                }
              ]
            },
            {
              type: "pie",
              name: "圆环-数据系列-动效（更新数据）",
              hoverAnimation: false,
              center: ["50%", "50%"],
              radius: [0, 0],
              animation: true,
              z: 6,
              slient: true,
              startAngle: 210,
              label: {
                normal: { show: false },
                emphasis: { show: false }
              },
              labelLine: {
                normal: { show: false },
                emphasis: { show: false }
              },
              itemStyle: {
                normal: {
                  borderWidth: 0,
                  shadowBlur: 8,
                  shadowColor: "rgba(255, 255, 255, 0.29)"
                }
              },
              data: [{
                  name: "已经过时间",
                  value: 0,
                  itemStyle: {
                    normal: {
                      color: "rgba(255,255,255,1)"
                    }
                  }
                },
                {
                  name: "剩余时间",
                  value: 0,
                  itemStyle: {
                    normal: {
                      color: "rgba(0,0,0,0)"
                    }
                  }
                },
                {
                  name: "时间缺口",
                  value: 33.3333333333,
                  itemStyle: {
                    normal: {
                      color: "rgba(0,0,0,0)"
                    }
                  }
                }
              ]
            },
            {
              type: "pie",
              name: "圆环-边框-静止（不用更新）",
              hoverAnimation: false,
              center: ["50%", "50%"],
              radius: [0, 0],
              animation: false,
              z: 7,
              slient: true,
              startAngle: 210,
              label: {
                normal: { show: false },
                emphasis: { show: false }
              },
              labelLine: {
                normal: { show: false },
                emphasis: { show: false }
              },
              itemStyle: {
                normal: {
                  shadowBlur: 20,
                  shadowColor: "rgba(160, 149, 231, 1)"
                }
              },
              data: [{
                  name: "剩余时间",
                  value: 66.6666666667,
                  itemStyle: {
                    normal: {
                      color: "rgba(0,0,0,0)",
                      borderWidth: 1,
                      borderColor: "rgba(196,196,252,1)"
                    }
                  }
                },
                {
                  name: "时间缺口",
                  value: 33.3333333333,
                  itemStyle: {
                    normal: {
                      color: "rgba(0,0,0,0)"
                    }
                  }
                }
              ]
            },
            {
              type: "gauge",
              name: "仪表盘小刻度-静止-（需要更新）",
              radius: 0,
              min: 0,
              max: 100,
              splitNumber: 4,
              z: 8,
              slient: true,
              animation: false,
              pointer: { show: false },
              startAngle: 210,
              endAngle: -30,
              axisLine: {
                lineStyle: {
                  color: [
                    [1, "rgba(0,0,0,0)"]
                  ],
                  width: 0
                }
              },
              axisTick: {
                splitNumber: 10,
                length: 0,
                lineStyle: {
                  color: "rgba(255,255,255,0.5)"
                }
              },
              axisLabel: { show: false },
              splitLine: { show: false },
              title: { show: false },
              detail: { show: false },
              data: []
            },
            {
              type: "gauge",
              name: "仪表盘大刻度-静止-（需要更新）",
              radius: 0,
              min: 0,
              max: 100,
              splitNumber: 3,
              z: 9,
              slient: true,
              animation: false,
              pointer: { show: false },
              startAngle: 210,
              endAngle: -30,
              axisLine: {
                lineStyle: {
                  color: [
                    [1, "rgba(0,0,0,0)"]
                  ],
                  width: 0
                }
              },
              axisTick: { show: false },
              axisLabel: { show: false },
              splitLine: {
                length: 0,
                lineStyle: {
                  color: "rgba(255,255,255,1)"
                }
              },
              title: {
                offsetCenter: [0, 0],
                color: "rgba(255,255,255,1)",
                fontSize: 0
              },
              detail: {
                formatter: function(value) {
                  return '{time|' + value.toFixed(2).toString().replace(".", ":") + '}\n{unit|min}';
                },
                rich: {
                  time: {
                    color: "rgba(255,255,255,1)",
                    fontSize: 0
                  },
                  unit: {
                    color: "rgba(255,255,255,1)",
                    fontSize: 0
                  }
                }
              },
              data: []
            }
          ]
        };

        var setData = function(data, type, total, increment, config) {
          chart.clear();
          var option = null;
          if (!increment) {
            option = extend({}, chartOption, getAdapterAdjustParam(type), getCustomConfig(type || 0, total || 15), data || {});
          } else {
            option = data;
            option.series[1].animation = false;
          }

          var s = parseFloat(document.documentElement.style.fontSize);
          switch (type) {
            case 0:
            case 2:
                option.series[4].title = {
                  show: false
                };
                option.series[4].detail.formatter = function(value) {
                    return "{v|" + (config.orderText || "") +"}";
                };
                option.series[4].detail.rich = {
                  v: {
                    fontSize: (32 / 75) * s,
                    color: "rgba(255,255,255,1)"
                  }
                };
                option.series[4].detail.offsetCenter = [0, (30 / 75) * s];
              break;
            case 1:
            case 3:
            case 4:
            case 5:
            case 6:
              if (option.series[4].data[0].value > total) {
                option.series[4].detail.rich.time.color = "rgba(255,201,2,1)";
              }
              break;
          }
          chart.setOption(option);
        };

        return function() {
          return {
            init: function() {
              gauge = document.querySelector(".gauge-chart");
              chart = echarts.init(gauge);
              return this;
            },
            setData: setData,
            getOption: function () {
              return chart.getOption();
            }
          }
        }
      })(),
      center: function () {
        this.removeInterval();
        this.$router.push({
          name: 'center'
        })
      },
      message: function () {
        this.removeInterval();
        this.$router.push({
          name: 'message'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/nurses.scss";
</style>



