<template>
  <div class="myOrderForm">
    <mOFheader title="我的订单"></mOFheader>
    <nav ref="nav">
      <span :class="nav === '全部'?'active':''" @click="change('全部')">全部</span>
      <span :class="nav === 5?'active':''" @click="change(5)">待确定</span>
      <span :class="nav === 7?'active':''" @click="change(7)">待出诊</span>
      <span :class="nav === 11?'active':''" @click="change(11)">服务中</span>
    </nav>
    <section id="index-scroll" ref="wrapper" :class="none === true ? 'none' : ''">
      <ul>
        <div class="refresh">
          <img src="../assets/images/myOrderForm/loading.png" alt=""><span>{{top}}...</span>
        </div>
        <li v-for="item in list" v-if="item.status === nav || nav === '全部' || (nav === 7 && item.status === 7 || nav === 7 && item.status === 9)" @click="details(item.status,item.id)">
          <h2 v-if="item.status === 9 ||item.status === 7">
            <span class="time time0">服务时间：{{item.book_time_text}}</span>
            <span class="status0">待出诊</span>
          </h2>
          <h2 v-if="item.status === 11">
            <span class="time time0">服务时间：{{item.book_time_text}}</span>
            <span class="status0">服务中</span>
          </h2>
          <h2 v-if="item.status === 5">
            <span class="time time1">服务时间：{{item.book_time_text}}</span>
            <span class="status1">待确定</span>
          </h2>
          <h2 v-if="item.status === 13 || item.status === 15 || item.status === 21 || item.status === 23 || item.status === 25">
            <span class="time time2">服务时间：{{item.book_time_text}}</span>
            <span class="status2">服务完成</span>
          </h2>
          <section class="detail">
            <img src="../assets/images/myOrderForm/ru.png" alt="">
            <div>
              <p>患者： <span>{{item.patient_name}}</span></p>
              <p>服务项目：<span>{{item.product_name}}</span></p>
            </div>
          </section>
          <section class="btn">
            <p></p>
            <div v-if="item.status === 5" class="btn0" @click.stop="edit(item.status,item.id)">确定订单</div>
            <div v-if="item.status === 7" class="btn0" @click.stop="edit(item.status,item.id)">确定出发</div>
            <div v-if="item.status === 9" class="btn0" @click.stop="edit(item.status,item.id)">确定到达服务地点</div>
          </section>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
  import {
    getOrders,
    confirmJiedan,
    confirmStart,
    confirmArrive
  } from '../service/getData'
  import mOFheader from '../components/mOFheader.vue'
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  export default {
    name: 'myOrderForm',
    data() {
      return {
        list: '',
        startY: -50,
        top: '刷新列表中',
        nav: '',
        pageNumber: 1,
        none: false
      }
    },
    components: {
      mOFheader
    },
    mounted: function() {
      getOrders(0, 1, this.$store.state.userId).then(res => {
        this.list = res.data.data;
        this.listScroll()
        if (this.$route.params.status === '全部') {
          this.nav = '全部'
        } else {
          this.nav = parseInt(this.$route.params.status)
        }
      })
    },
    methods: {
      listScroll: function() {
        var that = this
        Vue.nextTick(() => {
          that.wrapper = new BScroll(that.$refs.wrapper, {
            startY: that.startY,
            click: true
          })
          if (that.wrapper.wrapperHeight < that.wrapper.scrollerHeight) {
            that.add = true
          }
          that.top = '刷新列表中'
          that.wrapper.on('scrollEnd', (pos) => {
            if (pos.y === 0) {
              that.pageNumber = 1
              that.startY = -50
              getOrders(0, 1, this.$store.state.userId).then(res => {
                this.list = res.data.data;
                this.listScroll()
              })
            } else if (pos.y === that.wrapper.maxScrollY) {
              that.startY = that.wrapper.maxScrollY
              that.pageNumber++
                getOrders(0, this.pageNumber, this.$store.state.userId).then(res => {
                  this.list = this.list.concat(res.data.data);
                  this.listScroll()
                })
            }
          })
        })
      },
      change: function(status) {
        this.nav = status
      },
      details: function(status, order_no) {
        switch (status) {
          case 5:
            this.$router.push({
              name: 'undetermined',
              params: {
                order_no: order_no
              }
            })
            break;
          case 7:
            this.$router.push({
              name: 'toBeSetOut',
              params: {
                order_no: order_no
              }
            })
            break;
          case 9:
            this.$router.push({
              name: 'toArrive',
              params: {
                order_no: order_no
              }
            })
            break;
          case 11:
            this.$router.push({
              name: 'server',
              params: {
                order_no: order_no
              }
            })
            break;
          case 13:
          case 15:
            this.$router.push({
              name: 'success',
              params: {
                order_no: order_no
              }
            })
            break;
          case 21:
          case 23:
          case 25:
            this.$router.push({
              name: 'cancel'
            })
            break;
          default:

        }
      },
      edit: function(status, order_no) {
        switch (status) {
          case 5:
            confirmJiedan(this.$store.state.userId, order_no).then(() => {
              getOrders(0, this.pageNumber, this.$store.state.userId).then(res => {
                this.list = res.data.data;
                Vue.nextTick(() => {
                  this.wrapper.scrollTo(0, -50)
                  if (this.$refs.wrapper.children[0].children.length === 1) {
                    this.none = true
                  } else {
                    this.none = false
                  }
                })
              })
            })
            break;
          case 7:
            confirmStart(this.$store.state.userId, order_no).then(() => {
              getOrders(0, this.pageNumber, this.$store.state.userId).then(res => {
                this.list = res.data.data;
                Vue.nextTick(() => {
                  this.wrapper.scrollTo(0, -50)
                  if (this.$refs.wrapper.children[0].children.length === 1) {
                    this.none = true
                  } else {
                    this.none = false
                  }
                })
              })
            })
            break;
          case 9:
            confirmArrive(this.$store.state.userId, order_no).then(() => {
              getOrders(0, this.pageNumber, this.$store.state.userId).then(res => {
                this.list = res.data.data;
                Vue.nextTick(() => {
                  this.wrapper.scrollTo(0, -50)
                  if (this.$refs.wrapper.children[0].children.length === 1) {
                    this.none = true
                  } else {
                    this.none = false
                  }
                })
              })
            })
            break;
          default:

        }
      }
    },
    watch: {
      nav: function() {
        Vue.nextTick(() => {
          this.wrapper.scrollTo(0, -50)
          if (this.$refs.wrapper.children[0].children.length === 1) {
            this.none = true
          } else {
            this.none = false
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/myOrderForm.scss';
</style>



