<template>
  <div class="myOrderForm">
    <mOFheader title="我的订单"></mOFheader>
    <nav ref="nav">
      <span :class="nav === '服务详情'?'active':''" @click="change($event)">服务详情</span>
      <span :class="nav === '常见问题'?'active':''" @click="change($event)">常见问题</span>
      <span :class="nav === '服务评论'?'active':''" @click="change($event)">服务评论</span>
    </nav>
    <section id="index-scroll" ref="wrapper">
      <ul>
        <div class="refresh">
          <img src="../assets/images/myOrderForm/loading.png" alt=""><span>{{top}}...</span>
        </div>
        <li v-for="x in list">
            <h2 class="qu">{{x.qu}}</h2>

            <h2 class="an">{{x.an}}</h2>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
  import mOFheader from '../components/mOFheader.vue'
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  export default {
    name: 'myOrderForm',
    data() {
      return {
        list: [{
            qu:'护士服务怎么样？',
            an:'护士服务非常专业，还会再下单！'
        },{
            qu:'护士服务怎么样？',
            an:'护士服务非常专业，还会再下单！省去跑医院来回折腾，也没有排队挂号的干扰，非常棒！！！！！！！'
        },{
            qu:'护士服务怎么样？跟医院的服务相比呢？过程中会不会很疼？有没有订过的...',
            an:'护士服务非常专业，还会再下单！省去跑医院来回折腾，也没有排队挂号的干扰'
        }],
        startY: -50,
        top: '刷新列表中',
        nav: '常见问题'
      }
    },
    components: {
      mOFheader
    },
    mounted: function() {
      this.listScroll()
    },
    methods: {
      listScroll: function() {
        var that = this
        Vue.nextTick(() => {
          that.wrapper = new BScroll(that.$refs.wrapper, {
            startY: that.startY,
            click: true
          })
          if (that.wrapper.wrapperHeight < that.wrapper.scrollerHeight) {
            that.add = true
          }
          that.top = '刷新列表中'
          that.wrapper.on('scrollEnd', (pos) => {
            if (pos.y === 0) {
              that.pageNumber = 1
              that.startY = -50
            } else if (pos.y === that.wrapper.maxScrollY) {
              that.startY = that.wrapper.maxScrollY
              that.pageNumber++
            }
          })
        })
      },
      change: function (e) {
        this.nav = e.target.innerText
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/myOrderForm.scss';

  /*修改背景*/
  .myOrderForm nav .active{
      background: url("../assets/images/myOrderForm/navbb.png") no-repeat center bottom;
      background-size: 100% 10%;
  }
  #index-scroll li{
    height:auto;
    margin-top:0;
    margin-bottom:20px;
    padding:35px 30px;
    h2{
        height:44px;
        line-height:44px;
        margin-bottom:30px;
        height:auto;
    }







    .qu{
        padding-left:60px;
        background:url(../assets/images/question/qu.png) no-repeat left top;
        background-size:44px 44px;
        color:#333;
        font-size:36px;
    }
    .an{
        padding-left:60px;
         background:url(../assets/images/question/an.png) no-repeat left top;
        background-size:44px 44px;
        margin-bottom:0;
        font-size:32px;
        color:#666;
    }
  }
</style>



