<template>
  <div class="orderHistory">
    <mOFheader title="订单记录"></mOFheader>
    <div class="navBox">
      <div class="nav">
        <span :class="fangshi === '按周查看' ? 'active' : ''" @click="change(0)">按周查看</span>
        <span :class="fangshi === '按月查看' ? 'active' : ''" @click="change(1)">按月查看</span>
      </div>
    </div>
    <div class="details">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import mOFheader from '../components/mOFheader.vue'
  export default {
    name: 'orderHistory',
    components: {
      mOFheader
    },
    data() {
      return {
        fangshi: '按周查看',
        date: '08月07日 - 08月13日'
      }
    },
    methods: {
      change: function(index) {
        var that = this;
        switch (index) {
          case 0:
            that.fangshi = '按周查看'
            that.date = '08月07日 - 08月13日'
            this.$router.push({
              name: 'orderHistoryWeek'
            })
            break;
          case 1:
            that.fangshi = '按月查看'
            that.date = '2017年08月'
            this.$router.push({
              name: 'month'
            })
            break;
          default:

        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/orderHistory.scss';
</style>



