<template>
  <div class="login">
    <div class="loginBox">
      <h2>手机验证</h2>
      <p>请输入您的手机号码，</p>
      <p>登录或注册您的慧兰医疗账号</p>
      <input class="tel" type="text" name="" placeholder="请输入手机号" v-model="tel">
      <i :class="tel !== '' ? 'telDelete' : ''" @click="telDelete"></i>
      <div class="yzm">
        <input type="text" name="" placeholder="请输入验证码" v-model="yzm">
        <i :class="yzm !== '' ? 'yzmDelete' : ''" @click="yzmDelete"></i>
        <span v-show="hq" class="hq" @click="yan">{{hqyzm}}<i v-show="hqyzm !== '获取验证码'">s</i></span>
        <span v-show="!hq">{{hqyzm}}<i v-show="hqyzm !== '获取验证码'">s</i></span>
      </div>
      <div v-show="yzm !== ''" class="loginBtn loginActive" @click="loginActive">登录</div>
      <div v-show="yzm === ''" class="loginBtn">登录</div>
    </div>
    <img src="../assets/images/login/logo.png" alt="">
  </div>
</template>

<script>
  import {
    sendCode,
    login
  } from '../service/getData'
  import {
    mapActions
  } from 'vuex'
  import {
    Toast
  } from 'vant';
  export default {
    name: 'login',
    data() {
      return {
        tel: '',
        yzm: '',
        hqyzm: '获取验证码',
        hq: false
      }
    },
    watch: {
      tel: function() {
        var that = this;
        if (/^[0-9]{11}$/.test(that.tel)) {
          that.hq = true
        } else {
          that.hq = false
        }
      }
    },
    methods: {
      ...mapActions([ // 从store上绑定的getters中载入需要的到此组件中
        'actionUserId'
      ]),
      telDelete: function() {
        this.tel = ''
      },
      yzmDelete: function() {
        this.yzm = ''
      },
      yan: function() {
        var that = this;
        that.hq = false;
        var hqyzmr = setInterval(function() {
          if (that.hqyzm === '获取验证码') {
            that.hqyzm = 60;
            sendCode(that.tel);
          } else if (that.hqyzm > 0) {
            that.hqyzm--
          } else {
            clearInterval(hqyzmr)
            that.hqyzm = '获取验证码';
            that.hq = true;
          }
          if (that.hqyzm === 58) {
            that.tishi = false
          }
        }, 1000)
      },
      loginActive: function() {
        let that = this;
        login(that.tel, that.yzm).then(res => {
          if (res.msg == "登录成功") {
            that.actionUserId(res.data.token);
            localStorage.userId = res.data.token
            that.$router.push({
              name: 'nursesindex'
            })
          } else {
            Toast('请输入正确验证码！');
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/login.scss';
</style>



