<template>
  <div class="page-nurses">

    <div class="page-content-top">

        <!-- 头部个人信息 start -->
        <div class="page-head">

            <!-- 头部个人头像及工具栏 start -->
            <div class="self-info">
                <div class="self-photo">
                    <img v-bind:src="myInfo.head" alt="">
                </div>
                <div class="self-toolbar">
                    <ul>
                        <li><a href="javascript:void(0);" class="icon icon-005"></a></li>
                        <li><a href="javascript:void(0);" class="icon icon-006"></a></li>
                    </ul>
                </div>
            </div>
            <!-- 头部个人头像及工具栏 end -->

            <!-- 头部订单信息 start -->
            <div class="self-order">
                <div class="self-order-createtime">
                    <p>{{order.createtime}}</p>
                    <p>最近订单开始时间</p>
                </div>
                <div class="self-order-num">
                    <p>{{order.count}}</p>
                    <p>今日订单数</p>
                </div>
            </div>
            <!-- 头部订单信息 end --> 
              
        </div>
        <!-- 头部个人信息 end -->

        <!-- 页面主体内容 start -->
        <div class="page-body">

            <!-- 仪表盘图表 start -->
            <div class="gauge-chart-box">
                <!-- 订单地址及操作按钮 start -->
                <div class="opts">
                    <p class="order-address">{{order.address}}</p>
                    <button class="btn btn-face-1" v-if="order.hasOrder&&order.duration<=15">确定订单</button>
                    <button class="btn btn-face-2" v-if="order.hasOrder&&order.duration>15&&order.duration<=50">确定出发</button>
                    <button class="btn btn-face-3" v-if="order.hasOrder&&order.duration>50">服务中</button>
                </div>
                <!-- 订单地址及操作按钮 end -->
                <div class="gauge-chart"></div>
            </div>
            <!-- 仪表盘图表 end -->
        </div> 
        <!-- 页面主体内容 end -->
    </div>

    <!-- 波浪动画 start -->
    <canvas ref="waveCanvas" class="wave-box"></canvas>
    <!-- 波浪动画 end -->  

    <!-- 页面底部菜单 start -->
    <div class="page-bottom">
        <ul class="clearfix">
            <li class="bbline">
                <div class="item brline">
                    <div class="nav-num colored-nav">{{order.confirm}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-confirm"></i>
                        <span class="nav-title-text">待确定订单</span>
                    </div>
                </div>
                <div class="item">
                    <div class="nav-num colored-nav">{{order.wait}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-wait"></i>
                        <span class="nav-title-text">待出诊订单</span>
                    </div>
                </div>
            </li>
            <li> 
                <div class="item brline">
                    <div class="nav-num colored-nav">{{order.service}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-service"></i>
                        <span class="nav-title-text">服务中订单</span>
                    </div>
                </div>
                <div class="item">
                    <div class="nav-num colored-nav">{{order.total}}</div>
                    <div class="nav-title">
                        <i class="nav-icon nav-icon-total"></i>
                        <span class="nav-title-text">全部订单</span>
                    </div>
                </div>
            </li>
        </ul>
    </div>
    <!-- 页面底部菜单 end -->

  </div>
</template>

<script>
export default {
    name: "NursesIndex",
    data: function(){
        return {

            // 个人信息
            myInfo: {
                head: "src/pages/nurses/images/nurses_default.jpg"
            },

            // 界面测试数据
            //订单状态0: 初始状态
            order0: {
                hasOrder: false,
                createtime: "00:00",
                count: 0,
                confirm: 0,
                wait: 0,
                service: 0,
                total: 0,
                addr: "",
                duration: 15
            },

            //订单状态1：待确定订单
            order1: {
                hasOrder: true,
                createtime: "09-09 12:00",
                count: 1,
                confirm: 1,
                wait: 0,
                service: 0,
                total: 1,
                address:"南京市鼓楼区龙宝西地园25栋601",
                duration: 15
            },

            //订单状态2：确定出发
            order2: {
                hasOrder: true,
                createtime: "09-09 12:00",
                count: 1,
                confirm: 1,
                wait: 0,
                service: 0,
                total: 1,
                address: "南京市鼓楼区龙宝西地园25栋601",
                duration: 50
            },

            //订单状态3：订单服务中 
            order3:{
                hasOrder: true,
                createtime: "09-09 12:00",
                count: 1,
                confirm: 1,
                wait: 0,
                service: 0,
                total: 1,
                address: "南京市鼓楼区龙宝西地园25栋601",
                duration: 60
            },
            order:{}
        }
    },
    beforeRouteEnter: (to, from, next) => {
        next((vm)=>{
            vm.$nextTick(function () {
                vm.winHEIGHT =  window.screen.height;
                vm.winWIDTH =  window.screen.width;
                vm.loadWave();
            })
        });
    },
    mounted:function(){
        this.order = this.order3;
        this.guageControl().init().setData({

            // 暂无订单
            /*series: [
                {},
                {
                    data: [
                        {value: 0},
                        {value: 0},
                        {value: 33.3333333333}
                    ]
                }, {}, {},{
                    data: [
                        {value: 0}
                    ]
                }
            ] */

            // 确定订单
            /*series: [
                {},{
                    data: [
                        {value: 8.03},
                        {value: 6.97},
                        {value: 7.5}
                    ]
                }, {}, {},{
                    data: [
                        {value: 8.03}
                    ]
                }
            ]*/

            // 确定出发
            /* series: [
                {},{
                    data: [
                        {value: 26.03},
                        {value: 29.97},
                        {value: 25}
                    ]
                }, {}, {},{
                    data: [
                        {value: 26.03}
                    ]
                }
            ]*/
            // 服务中
            series: [
                {},{
                    data: [
                        {value: 60},
                        {value: 0},
                        {value: 30}
                    ]
                }, {}, {},{
                    data: [
                        {value: 60.03}
                    ]
                }
            ]  
        }, 3, 60);
        /*
           0: 0, 15,
           1: 1, 15,
           2: 2, 50,
           3: 3, 60
        */
    },
    methods:{
        loadWave: function(){ 
            let graph = this.$refs.waveCanvas;
            let ctx = graph.getContext("2d");
            graph.width = this.winWIDTH;
            graph.height = 140;
            window.requestAnimFrame = (function(){ 
                return window.requestAnimationFrame  || 
                    window.webkitRequestAnimastionFrame || 
                    window.mozRequestAnimationFrame || 
                    function( callback ){ 
                    window.setTimeout(callback, 1000 / 60); 
                }; 
            })();
            // 波浪大小
            var boHeight = 0;
            var boHeights = [20,25,20];
            var posHeight = 0;
            var posHeights = [100,60,60];
            //初始角度为0 
            var step = 0; 
            //定义三条不同波浪的颜色
           var lines = ["rgba(152, 135, 247,0.6)","rgba(152, 135, 247,0.4)","rgba(152, 135, 247,0.2)"]; 

            var angle = 0;
            var deltaHeight = 0; 
            var deltaHeightRight = 0;         

            function loop(){ 
                ctx.clearRect(0,0,graph.width,graph.height); 
                step++; 
                //画3个不同颜色的矩形 
                for(var j = lines.length - 1; j >= 0; j--) { 
                    posHeight = posHeights[j];
                    boHeight = boHeights[j];
                    ctx.fillStyle = lines[j];  
                    ctx.beginPath();  
                    angle = (step+j*50)*Math.PI/180; 
                    deltaHeight = Math.sin(angle*4/3) * boHeight;
                    deltaHeightRight = Math.cos(angle*4/3) * boHeight;
                    ctx.moveTo(0, posHeight + deltaHeight);           
                    if (j == 0) {                      
                        ctx.bezierCurveTo(graph.width / 2, posHeight+boHeight+deltaHeight, graph.width/2, posHeight-boHeight-deltaHeightRight, graph.width, posHeights[2] + deltaHeightRight); 

                    } else if (j == 1) {                    
                        ctx.bezierCurveTo(graph.width/2, posHeight+boHeight+deltaHeight, graph.width/2, posHeight-boHeight-deltaHeightRight, graph.width, posHeight + deltaHeightRight); 
                    } else if (j == 2) {
                        ctx.bezierCurveTo(graph.width/2, posHeight-boHeight-deltaHeight, graph.width/2, posHeight+boHeight+deltaHeightRight, graph.width, 90 + deltaHeightRight); 
                    }

                    ctx.lineTo(graph.width, graph.height); 
                    ctx.lineTo(0, graph.height); 
                    ctx.lineTo(0, posHeight+deltaHeight); 
                    ctx.closePath(); 
                    ctx.fill(); 
                    ctx.globalCompositeOperation = "destination-over";
                }
                requestAnimFrame(loop);
            } 
            loop(); 
        },
        guageControl: (function () {

            // 判断是否为数组
            var isArray = function (o) {
                return Object.prototype.toString.call(o) == "[object Array]";
            };

            // 判断是否为对象
            var isObject = function (o) {
                return Object.prototype.toString.call(o) == "[object Object]";
            };

            // 深度拷贝对象实现
            var extend = (function () {

                var _extend = function (target, source) {

                    if (isArray(source)) {
                        for (var i = 0; i < source.length; i++) {
                            if (Array.isArray(source[i])) {
                                if (typeof target[i] == "undefined" || !isArray(target[i])) {
                                    target[i] = []; 
                                }
                                _extend(target[i], source[i]);
                            } else if (isObject(source[i])) {
                                if (typeof target[i] == "undefined" || !isObject(target[i])) {
                                    target[i] = {};
                                }
                                _extend(target[i], source[i]);
                            } else {
                                target[i] = source[i];
                            }
                        }
                    } else if (isObject(source)) {
                        for (var property in source) {
                            if (isArray(source[property])) {
                                if (typeof target[property] == "undefined" || !isArray(target[property])){
                                    target[property] = [];  
                                }
                                _extend(target[property], source[property]);
                            } else if (isObject(source[property])) {
                                if (typeof target[property] == "undefined" || !isObject(target[property])) {
                                    target[property] = {};
                                }
                                _extend(target[property], source[property]);
                            } else {
                                target[property] = source[property];
                            }
                        }
                    } else {
                        target = source;
                    }           
                };

                return function () {
                    var i = 1,
                        len = arguments.length,
                        target = arguments[0],
                        arg;

                    if (!target) return {}; 

                    if (len == 1) {
                        target = _extend({}, target);
                    } else {
                        for (; i < len; i++) {
                            arg = arguments[i];
                            _extend(target, arg);
                        }
                    }

                    return target;
                }
            })();

            var echarts = require("echarts"),
                gauge = null,
                chart = null;

            // 自适应获取参数
            var getChartAdjustParam = function (type) {
                var currentFontSize = parseFloat(document.documentElement.style.fontSize);

                return {
                    sPie1: {
                        radius: (162 / 75) * currentFontSize,
                        borderWidth: (5 / 75) * currentFontSize
                    },
                    sPie2: {
                        radius: (166 / 75) * currentFontSize
                    },
                    sPie3: {
                        radius: (166 / 75) * currentFontSize
                    },
                    lPie1: {
                        radius: (189/75) * currentFontSize
                    },
                    lPie2: {
                        radius: (189/75) * currentFontSize
                    },
                    lPie3: {
                        radius: (189/75) * currentFontSize
                    },
                    sGauge: {
                        radius: (209 / 75) * currentFontSize,
                        axisTick: {
                            length: (9 / 75) * currentFontSize
                        }
                    },
                    lGauge: {
                        radius: (225 / 75) * currentFontSize,
                        splitLine: {
                            length: (27 / 75) * currentFontSize
                        },
                        title: {
                            fontSize: (34 / 75) * currentFontSize,
                            offsetCenterY: -(74 / 75) * currentFontSize
                        },
                        detail: {
                            time: {
                                fontSize: (82 / 75) * currentFontSize
                            },
                            unit: {
                                fontSize: (28 / 75) * currentFontSize,
                                lineHeight: (46 / 75) * currentFontSize
                            },
                            offsetCenterY: (35 / 75) * currentFontSize
                        }
                    },
                    graphic: {
                        width: (225*2/75) * currentFontSize,
                        height: (225*2/75) * currentFontSize,
                        fontSize: (18/75) * currentFontSize,
                        ts: (function () {
                            var gaugeWidth = gauge.offsetWidth,
                                gaugeHeight = gauge.offsetHeight;
                            var coordX = 0,
                                coordY = 0;
                            var r = (225 / 75) * currentFontSize;

                            var o = null;
                            switch (type) {
                                case 0:
                                case 1:
                                    o = {
                                        t1: function () {
                                            var deg = 30 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 38;
                                            var y = r * Math.sin(deg),
                                                y = y - 16.5;
                                            return {
                                                bottom: y,
                                                left: x
                                            }
                                        }(),
                                        t2: function () {
                                            var deg = 50 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r + x - 8.5;
                                            var y = r * Math.sin(deg),
                                                y = r + y + 10.5;
                                            return {
                                                bottom: y,
                                                left: x
                                            }
                                        }(),
                                        t3:{ invisible: true },
                                        t4:{ invisible: true },
                                        t5:{ invisible: true },
                                        t6:{ invisible: true },
                                        t7:{ invisible: true },
                                        t8: function () {
                                            var deg = 50 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 24;
                                            var y = r * Math.sin(deg),
                                                y = r - y - 21.5;
                                            return {
                                                top: y,
                                                left: x
                                            }
                                        }(),
                                        t9: function () {
                                            var deg = 30 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 47;
                                            var y = r * Math.sin(deg),
                                                y = y - 15.5;
                                            return {
                                                bottom: y,
                                                right: x
                                            }
                                        }()
                                    }
                                    break;
                                case 2:
                                    o = {
                                        t1: function () {
                                            var deg = 30 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 38;
                                            var y = r * Math.sin(deg),
                                                y = y - 16.5;
                                            return {
                                                bottom: y,
                                                left: x
                                            }
                                        }(),
                                        t2: function () {
                                            var deg = 18 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 46;
                                            var y = r * Math.sin(deg),
                                                y = r + y - 2.5;
                                            return {
                                                bottom: y,
                                                left: x
                                            }
                                        }(),
                                        t3: function () {
                                            var deg = 66 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 25;
                                            var y = r * Math.sin(deg),
                                                y = r - y - 18.5;
                                            return {
                                                top: y,
                                                left: x
                                            }
                                        }(),
                                        t4: function () {
                                            var deg = 66 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 25;
                                            var y = r * Math.sin(deg),
                                                y = r - y - 18.5;
                                            return {
                                                top: y,
                                                right: x
                                            }
                                        }(),
                                        t5: function () {
                                            var deg = 18 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 44;
                                            var y = r * Math.sin(deg),
                                                y = r - y - 13;
                                            return {
                                                top: y,
                                                right: x
                                            }
                                        }(),
                                        t6: function () {
                                            var deg = 30 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 47;
                                            var y = r * Math.sin(deg),
                                                y = y - 15.5;
                                            return {
                                                bottom: y,
                                                right: x
                                            }
                                        }(),
                                        t7:{ invisible: true },
                                        t8:{ invisible: true },
                                        t9:{ invisible: true }
                                    }
                                    break;
                                case 3:
                                    o = {
                                        t1: function () {
                                            var deg = 30 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 38;
                                            var y = r * Math.sin(deg),
                                                y = y - 16.5;
                                            return {
                                                bottom: y,
                                                left: x
                                            }
                                        }(),
                                        t2: function () {
                                            var deg = 10 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 45;
                                            var y = r * Math.sin(deg),
                                                y = r + y - 3.5;
                                            return {
                                                bottom: y,
                                                left: x
                                            }
                                        }(),
                                        t3: function () {
                                            var deg = 50 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 29;
                                            var y = r * Math.sin(deg),
                                                y = r - y - 19.5;
                                            return {
                                                top: y,
                                                left: x
                                            }
                                        }(),
                                        t4: function () {
                                            var x = r - 15;
                                            var y = 0 - 13 - 6;
                                            return {
                                                top: y,
                                                right: x
                                            }
                                        }(),
                                        t5: function () {
                                            var deg = 50 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 29;
                                            var y = r * Math.sin(deg),
                                                y = r - y - 19.5;
                                            return {
                                                top: y,
                                                right: x
                                            }
                                        }(),
                                        t6: function () {
                                            var deg = 10 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 45;
                                            var y = r * Math.sin(deg),
                                                y = r + y - 3.5;
                                            return {
                                                bottom: y,
                                                right: x
                                            }
                                        }(),
                                        t7: function () {
                                            var deg = 30 * Math.PI / 180;
                                            var x = r * Math.cos(deg),
                                                x = r - x - 47;
                                            var y = r * Math.sin(deg),
                                                y = y - 15.5;
                                            return {
                                                bottom: y,
                                                right: x
                                            }
                                        }(),
                                        t8:{ invisible: true },
                                        t9:{ invisible: true }
                                    }
                                    break;
                            }

                            o.circle = {
                                cx: 100,
                                cy: 1,
                                r: (160 / 75) * currentFontSize
                            };

                            return o;
                        })()
                    }
                }
            };

            // 自适应到饼图格式
            var getAdapterAdjustParam = function (type) {
                var param = getChartAdjustParam(type);
                return {
                    graphic: [
                        {
                            width: param.graphic.width,
                            height: param.graphic.height,
                            children: [
                                {
                                    invisible: param.graphic.ts.t1.invisible,
                                    bottom: param.graphic.ts.t1.bottom,
                                    left: param.graphic.ts.t1.left,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t2.invisible,
                                    bottom: param.graphic.ts.t2.bottom,
                                    left: param.graphic.ts.t2.left,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t3.invisible,
                                    top: param.graphic.ts.t3.top,
                                    left: param.graphic.ts.t3.left,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t4.invisible,
                                    top: param.graphic.ts.t4.top,
                                    right: param.graphic.ts.t4.right,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t5.invisible,
                                    top: param.graphic.ts.t5.top,
                                    right: param.graphic.ts.t5.right,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t6.invisible,
                                    bottom: param.graphic.ts.t6.bottom,
                                    right: param.graphic.ts.t6.right,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t7.invisible,
                                    bottom: param.graphic.ts.t7.bottom,
                                    right: param.graphic.ts.t7.right,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t8.invisible,
                                    top: param.graphic.ts.t8.top,
                                    left: param.graphic.ts.t8.left,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    invisible: param.graphic.ts.t9.invisible,
                                    bottom: param.graphic.ts.t9.bottom,
                                    right: param.graphic.ts.t9.right,
                                    style: {
                                        fontSize: param.graphic.fontSize
                                    }
                                },
                                {
                                    shape: {
                                        cx: param.graphic.ts.circle.cx,
                                        cy: param.graphic.ts.circle.cy,
                                        r: param.graphic.ts.circle.r
                                    }
                                }
                            ]
                        }
                    ],
                    series: [
                        {
                            radius: [param.sPie1.radius, param.lPie1.radius],
                            itemStyle: {
                                normal: {
                                    borderWidth: param.sPie1.borderWidth
                                }
                            }
                        },
                        {
                            radius: [param.sPie2.radius, param.lPie2.radius]
                        },
                        {
                            radius: [param.sPie3.radius, param.lPie3.radius]
                        },
                        {
                            radius: param.sGauge.radius,
                            axisTick: {
                                length: param.sGauge.axisTick.length
                            }
                        },
                        {
                            radius: param.lGauge.radius,
                            splitLine: {
                                length: param.lGauge.splitLine.length
                            },
                            title: {
                                fontSize: param.lGauge.title.fontSize,
                                offsetCenter: [0, param.lGauge.title.offsetCenterY]
                            },
                            detail: {
                               rich: {
                                   time: {
                                        fontSize: param.lGauge.detail.time.fontSize
                                    },
                                    unit: {
                                        fontSize: param.lGauge.detail.unit.fontSize,
                                        lineHeight: param.lGauge.detail.unit.lineHeight
                                    }
                               },
                               offsetCenter: [0, param.lGauge.detail.offsetCenterY]
                            }
                        }
                    ]
                }
            };

            // 自定义配置 0 无订单 1确定订单 2 确定出发 3 服务中
            var getCustomConfig = function (type, total) {
                var param = {
                    series: [{},{},{},{},{}]
                };

                // 分割段数设置
                var splitNumber = 3;
                if (total > 15 && total <= 50) {
                    splitNumber = 5;
                } else if (total > 50 && total <= 60){
                    splitNumber = 6;
                }
                param.series[3].splitNumber = splitNumber;
                param.series[4].splitNumber = splitNumber;

                switch (type) {
                    case 0:
                    case 1:
                    case 2:
                        break;
                    case 3:
                        param.series[4].detail = {};
                        param.series[4].detail.rich = {
                            time: {
                                color: "rgba(255,201,2,1)"
                            },
                        }   
                        break;
                }

                return param;
            };

            // 仪表盘配置
            var chartOption = {
                title: { show: false },
                tooltip: { show: false },
                legend: { show: false },
                graphic: [
                    {
                        type: "group",
                        width: 0,
                        height: 0,
                        bounding: "raw",
                        z: -1,
                        top: "middle",
                        left: "center",         
                        children: [
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "0min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "10min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "20min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "30min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "40min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "50min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "60min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "5min"
                                }
                            },
                            {
                                type: "text",
                                z: 2,
                                style: {
                                    fill: "#fff",
                                    text: "15min"
                                }
                            },
                            {
                                type: "circle",
                                z: 1,
                                left: "center",
                                top: "center",
                                shape: {
                                    cx: 0,
                                    cy: 0,
                                    r: 0
                                },
                                style: {
                                    fill: new echarts.graphic.LinearGradient(0, 0, 0, 1,
                                        [
                                            {offset: 0, color: "rgba(137,119,246,1)"},
                                            {offset: 0.8, color: "rgba(137,119,246,0)"}
                                        ]
                                    )
                                }
                            }
                        ]
                    }
                ],
                series: [
                    {
                        type: "pie",
                        name: "圆环-底色-静止（不用更新）",
                        hoverAnimation: false,
                        center: ["50%", "50%"],
                        radius: [0, 0],
                        animation: false,
                        z: 5,
                        slient: true,
                        startAngle: 210,
                        label: {
                            normal: { show: false },
                            emphasis: { show: false }
                        },
                        labelLine: {
                            normal: { show: false },
                            emphasis: { show: false }
                        },
                        itemStyle: {
                            normal:{
                                shadowBlur: 30,
                                shadowColor: "rgba(0, 0, 0, 0.15)",
                                shadowOffsetY: 8,
                                borderWidth: 0,
                                borderColor: "rgba(167,156,234,0.9)"
                            }
                        },
                        data: [
                            {
                                name: "剩余时间",
                                value: 66.6666666667,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(0,0,0,0)"
                                    }
                                }
                            },
                            {
                                name: "时间缺口",
                                value: 33.3333333333,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(0,0,0,0)",
                                        borderWidth: 0
                                    }
                                } 
                            }
                        ]
                    },
                    {
                        type: "pie",
                        name: "圆环-数据系列-动效（更新数据）",
                        hoverAnimation: false,
                        center: ["50%", "50%"],
                        radius: [0, 0],
                        animation: true,
                        z: 6,
                        slient: true,
                        startAngle: 210,
                        label: {
                            normal: { show: false },
                            emphasis: { show: false }
                        },
                        labelLine: {
                            normal: { show: false },
                            emphasis: { show: false }
                        },
                        itemStyle: {
                            normal: {
                                borderWidth: 0,
                                shadowBlur: 8,
                                shadowColor: "rgba(255, 255, 255, 0.29)"
                            }
                        },
                        data: [
                            {
                                name: "已经过时间",
                                value: 0,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(255,255,255,1)"
                                    }
                                }
                            },
                            {
                                name: "剩余时间",
                                value: 0,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(0,0,0,0)"
                                    }
                                }
                            },
                            {
                                name: "时间缺口",
                                value: 33.3333333333,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(0,0,0,0)"
                                    }
                                } 
                            }
                        ]
                    },        
                    {
                        type: "pie",
                        name: "圆环-边框-静止（不用更新）",
                        hoverAnimation: false,
                        center: ["50%", "50%"],
                        radius: [0, 0],
                        animation: false,
                        z: 7,
                        slient: true,
                        startAngle: 210,
                        label: {
                            normal: { show: false },
                            emphasis: { show: false }
                        },
                        labelLine: {
                            normal: { show: false },
                            emphasis: { show: false }
                        },
                        itemStyle: {
                            normal:{
                                shadowBlur: 20,
                                shadowColor: "rgba(160, 149, 231, 1)"
                            }
                        },
                        data: [
                            {
                                name: "剩余时间",
                                value: 66.6666666667,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(0,0,0,0)",
                                        borderWidth: 1,
                                        borderColor: "rgba(196,196,252,1)"
                                    }
                                }
                            },
                            {
                                name: "时间缺口",
                                value: 33.3333333333,
                                itemStyle: {
                                    normal:{
                                        color: "rgba(0,0,0,0)"
                                    }
                                } 
                            }
                        ]
                    },
                    {
                        type: "gauge",
                        name: "仪表盘小刻度-静止-（需要更新）",
                        radius: 0,
                        min: 0,
                        max: 100,
                        splitNumber: 4,
                        z: 8,
                        slient: true,
                        animation: false,
                        pointer: { show: false },
                        startAngle: 210,
                        endAngle: -30,
                        axisLine: {
                            lineStyle: {
                                color: [[1, "rgba(0,0,0,0)"]],
                                width: 0
                            }
                        },
                        axisTick: {
                            splitNumber: 10,
                            length: 0,
                            lineStyle: {
                                color: "rgba(255,255,255,0.5)"
                            }
                        },
                        axisLabel: { show: false },
                        splitLine: { show: false },
                        title: { show: false },
                        detail: { show: false },
                        data: [ {name: "产后催乳", value: 0} ]
                    },
                    {
                        type: "gauge",
                        name: "仪表盘大刻度-静止-（需要更新）",
                        radius: 0,
                        min: 0,
                        max: 100,
                        splitNumber: 3,
                        z: 9,
                        slient: true,
                        animation: false,
                        pointer: { show: false },
                        startAngle: 210,
                        endAngle: -30,
                        axisLine: {
                            lineStyle: {
                                color: [[1, "rgba(0,0,0,0)"]],
                                width: 0
                            }
                        },
                        axisTick: { show: false },
                        axisLabel: { show: false },
                        splitLine: {
                            length: 0,
                            lineStyle: {
                                color: "rgba(255,255,255,1)"
                            }
                        },
                        title: {
                            offsetCenter: [0, 0],
                            color: "rgba(255,255,255,1)",
                            fontSize: 0
                        },
                        detail: {
                            formatter: function (value) {
                                return '{time|' + value.toString().replace(".", ":") + '}\n{unit|min}';
                            },
                            rich: {
                                time: {
                                    color: "rgba(255,255,255,1)",
                                    fontSize: 0
                                },
                                unit: {
                                    color: "rgba(255,255,255,1)",
                                    fontSize: 0
                                }
                            }
                        },
                        data: [ {name: "产后催乳", value: 0} ]
                    }
                ]
            };
           
            var setData = function (data, type, total) {
                chart.clear();
                var option = extend({}, chartOption, getAdapterAdjustParam(type), getCustomConfig(type || 0, total || 15), data || {});

                var s = parseFloat(document.documentElement.style.fontSize);
                if (type == 0) {
                    option.series[4].title = {
                        show: false
                    };
                    option.series[4].detail.formatter = function (value) {
                        return '{v|暂无订单}';
                    },
                    option.series[4].detail.rich = {
                        v: {
                            fontSize: (32/75) * s,
                            color: "rgba(255,255,255,1)"
                        }
                    },
                    option.series[4].detail.offsetCenter = [0, (30/75)*s];
                } 

                chart.setOption(option);
            };

            return function () {
               return { 
                    init: function () {
                        gauge = document.querySelector(".gauge-chart");
                        chart = echarts.init(gauge);
                        return this;
                    },
                    setData: setData
               }
            }
        })()
    }
}
</script>

<style lang="scss" scoped>
@import "../../assets/css/nurses.scss";
</style>




