<template>
  <div class="toBeSetOut">
    <mOFheader title="订单详情"></mOFheader>
    <div class="logoBox">
      <img src="../assets/images/DDdetail/dcf.png" alt="">
      <div class="banner">
        <p><img src="../assets/images/myOrderForm/ru.png" alt=""><span>{{main.product.product_name}}</span></p>
        <div class="label">
          <span>产妇护理包</span>
          <span>护理员职业责任保险</span>
        </div>
      </div>
    </div>
    <div class="user">
      <section>
        <p><i></i><span>患者姓名</span></p>
        <p>{{main.order_patient.patient_name}}</p>
      </section>
      <section>
        <p><i></i><span>患者年龄</span></p>
        <p>{{main.order_patient.patient_age}}岁</p>
      </section>
      <section>
        <p><i></i><span>患者性别</span></p>
        <p>{{main.order_patient.patient_gender}}</p>
      </section>
      <section>
        <p><i></i><span>是否本院患者</span></p>
        <p v-if="main.order_patient.is_hospital === 1">是</p>
        <p v-else>否</p>
      </section>
    </div>
    <div class="address">
      <section>
        <span>服务时间</span>
        <p>{{main.book_time_text}}</p>
      </section>
      <section>
        <span>服务地点</span>
        <p>{{main.order_address}}</p>
      </section>
      <section>
        <span>病情详情</span>
        <p>{{main.medical_desc}}</p>
      </section>
    </div>
    <div class="btn">
      <a href="#" class="btn1" @click="queren">确定出发</a>
      <a :href="telNum(main.patient_phone)" class="btn2"><img src="../assets/images/DDdetail/tel.png" alt="">联系患者</a>
    </div>
  </div>
</template>

<script>
  import mOFheader from '../components/mOFheader.vue'
  import {
    getOrderDetail,
    confirmStart
  } from '../service/getData'
  export default {
    name: 'toBeSetOut',
    data() {
      return {
        main: ''
      }
    },
    mounted: function() {
      getOrderDetail(this.$route.params.order_no, this.$store.state.userId).then(res => {
        this.main = res.data
        console.log(this.main);
      })
    },
    methods: {
      queren() {
        confirmStart(this.$store.state.userId, this.$route.params.order_no).then(res => {
          if (res.msg === "提交成功") {
            this.$router.push({
              name: 'nursesindex'
            })
          }
        })
      },
      telNum: (phoneNum) => {
        if (typeof phoneNum !== 'undefined') {
          return 'tel:' + phoneNum
        } else {
          return ''
        }
      }
    },
    components: {
      mOFheader
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/toBeSetOut.scss';
</style>



