import fetch from '../config/fetch.js'
import qs from 'qs'
/**
 * 获取验证码
 */
export const sendCode = (phone) => {
  return fetch('/sms/sendCode', {
    phone: phone,
    sendType: 1,
    userType: 2
  }, 'POST');
}
/**
 * 登陆
 */
export const login = (phone, verify) => {
  return fetch('/nurse/login', {
    phone: phone,
    verify: verify,
    loginType: 2
  }, 'POST');
}
/**
 * 登出
 */
export const logout = (token) => {
  return fetch('nurse/logout', {
    token: token
  }, 'POST');
}
/**
 * 根据护士Id获取护士信息
 */
export const nurseCenter = (token) => {
  return fetch('/nurse/nurseCenter', {
    token: token
  });
}
/**
 * 根据用户类型、用户Id获取消息数据
 */
export const getMsgs = (token, page) => {
  return fetch('/message/getNurseMsgs', {
    token: token,
    page: page,
    pageRows: 10
  });
}
/**
 * 根据用户Id获取出诊数据
 */
export const orderVisitList = (token, begin_time, end_time) => {
  if (end_time) {
    return fetch('/order/visitList', {
      begin_time: begin_time,
      end_time: end_time,
      token: token
    })
  } else {
    return fetch('/order/visitList', {
      begin_time: begin_time,
      end_time: begin_time,
      token: token
    })
  }

}
/**
 * 根据订单状态获取订单
 */
export const getOrders = (status, page, token) => {
  return fetch('/order/getOrders', {
    status: status,
    page: page,
    pageRows: 15,
    token: token
  });
}
/**
 * 根据订单编号获取订单信息
 */
export const getOrderDetail = (order_id, token) => {
  return fetch('/order/getOrderDetail', {
    order_id: order_id,
    token: token
  });
}
/**
 * 护士资料编辑
 */
export const nurseEdit = (realname, age, gender, phone, id_card, token) => {
  return fetch('/nurse/edit', {
    realname: realname,
    age: age,
    gender: gender,
    phone: phone,
    id_card: id_card,
    token: token
  }, 'POST');
}
/**
 * 根据用户类型、用户Id删除消息数据
 */
export const delMsgs = (token, msgIds) => {
  return fetch('/message/delMsgs', {
    token: token,
    msgIds: msgIds
  }, 'POST');
}
/**
 * 根据消息Id标记是否已读未读
 */
export const changeStatus = (token, msgIds) => {
  return fetch('/message/changeStatus', {
    token: token,
    msgIds: msgIds
  }, 'POST');
}
/**
 * 护士确认订单
 */
export const confirmJiedan = (token, orderId) => {
  return fetch('/order/confirmJiedan', {
    token: token,
    orderId: orderId
  }, 'POST');
}
/**
 * 护士确认出门
 */
export const confirmStart = (token, orderId) => {
  return fetch('/order/confirmStart', {
    token: token,
    orderId: orderId
  }, 'POST');
}
/**
 * 护士确认到达
 */
export const confirmArrive = (token, orderId) => {
  return fetch('/order/confirmArrive', {
    token: token,
    orderId: orderId
  }, 'POST');
}
/**
 * 文件上传
 */
export const fileUpload = (upfile, token) => {
  return fetch('/file/upload?token=' + token, {
    upfile: upfile
  }, 'POST');
}
/**
 * 订单记录
 */
export const getVisit = (data) => {
  return fetch('/order/getHistory',data, 'GET');
}
/**
 * 出诊记录
 */
export const weekList = (data) => {
  return fetch('/order/getVisit',data, 'GET');
}



