<template>
  <div class="photo">
    <!-- <canvas id="myCanvas"></canvas> -->
    <div class="top"></div>
    		<vueCropper
						ref="cropper"
						:img="imghead.img"
						:outputSize="imghead.size"
						:info="imghead.info"
						:canScale="imghead.canScale"
						:autoCrop="imghead.autoCrop"
						:autoCropWidth="imghead.autoCropWidth"
						:autoCropHeight="imghead.autoCropHeight"
						:fixed="imghead.fixed"
						:fixedNumber="imghead.fixedNumber"
					></vueCropper>
    <div class="bottom">
      <p>
        <!-- <span @click="uploadImg" ><label class="btn" for="upload2">上传本地图片</label></span> -->
        <label class="btn" for="upload2">上传本地图片</label>
				<input type="file" id="upload2" style="position:absolute; clip:rect(0 0 0 0);" accept="image/png, image/jpeg, image/gif, image/jpg" @change="uploadImg($event, 2)">
        <i  @click="down('blob234')">确认</i>
      </p>
    </div>
  </div>
</template>
<script>
import vueCropper from 'vue-cropper'
export default {
  name: 'photo',
  data: function () {
    return {
			model: false,
			modelSrc: '',
		  crap: false,
			previews: {},
			imghead: {
				img: 'http://osygrby6k.bkt.clouddn.com/503993_62595320.jpg',
				info: true,
				size: 1,
				// outputType: 'jpeg',
				canScale: true,
				autoCrop: true,
				// 只有自动截图开启 宽度高度才生效
				autoCropWidth: 300,
				autoCropHeight: 250,
				fixed: true,
				fixedNumber: [4, 3]
			},
			downImg: '#'
    }
  },
	methods: {
		// 实时预览函数
		realTime (data) {
			this.previews = data
		},
		down (type) {
			// event.preventDefault()
			var aLink = document.createElement('a')
			aLink.download = 'demo'
			// 输出
			if (type === 'blob') {
				this.$refs.cropper.getCropBlob((data) => {
					// this.downImg = window.URL.createObjectURL(data)
          // aLink.href = window.URL.createObjectURL(data)
					// aLink.click()
				})
			} else {
				this.$refs.cropper.getCropData((data) => {
					// this.downImg = data
          // aLink.href = data
					// aLink.click()
				})
			}
		},
		uploadImg (e, num) {
			//上传图片
			// this.option.img
			var file = e.target.files[0]
			if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
				 alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')
				 return false
			 }
			var reader = new FileReader()
			reader.onload = (e) => {
				let data
				if (typeof e.target.result === 'object') {
					// 把Array Buffer转化为blob 如果是base64不需要
					data = window.URL.createObjectURL(new Blob([e.target.result]))
				} else {
					data = e.target.result
				}
				if (num === 1) {
					this.option.img = data
				} else if (num === 2) {
					this.imghead.img = data
				}
			}
			// 转化为base64
			// reader.readAsDataURL(file)
			// 转化为blob
			reader.readAsArrayBuffer(file)
		},
		imgLoad (msg) {
			console.log(msg)
		}
	},
	components: {
		vueCropper
	},
	mounted () {
		// hljs.configure({useBR: true})
		var list = [].slice.call(document.querySelectorAll('pre code'))
		list.forEach((val, index) => {
		  hljs.highlightBlock(val)
		})
	}
}
</script>

<style lang="scss" scoped>
  @import '../assets/css/photo.scss';
	.content {
		margin: auto;
		max-width: 1200px;
		margin-bottom: 100px;
	}
	.test-button {
		display: flex;
		flex-wrap: wrap;
	}
	.btn {
		display: inline-block;
		line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #c0ccda;
    color: #1f2d3d;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    margin:20px 10px 0px 0px;
    padding: 9px 15px;
    font-size: 14px;
    border-radius: 4px;
    color: #fff;
    background-color: #50bfff;
    border-color: #50bfff;
    transition: all .2s ease;
		text-decoration: none;
		user-select: none;
	}
	.des {
		line-height: 30px;
	}
	code.language-html {
		padding: 10px 20px;
		margin: 10px 0px;
		display: block;
		background-color: #333;
		color: #fff;
		overflow-x: auto;
		font-family: Consolas, Monaco, Droid, Sans, Mono, Source, Code, Pro, Menlo, Lucida, Sans, Type, Writer, Ubuntu, Mono;
		border-radius: 5px;
		white-space: pre;
	}
	.show-info {
		margin-bottom: 50px;
	}
	.show-info h2 {
		line-height: 50px;
	}
	/*.title, .title:hover, .title-focus, .title:visited {
		color: black;
	}*/
	.title {
		display: block;
		text-decoration: none;
		text-align: center;
		line-height: 1.5;
		margin: 20px 0px;
		background-image: -webkit-linear-gradient(left,#3498db,#f47920 10%,#d71345 20%,#f7acbc 30%,#ffd400 40%,#3498db 50%,#f47920 60%,#d71345 70%,#f7acbc 80%,#ffd400 90%,#3498db);
    color: transparent;
    -webkit-background-clip: text;
    background-size: 200% 100%;
    animation: slide 5s infinite linear;
		font-size: 40px;
	}
	.test {
	  height: 500px;
	}
	.model {
		position: fixed;
		z-index: 10;
		width: 100vw;
		height: 100vh;
		overflow: auto;
		top: 0;
		left: 0;
		background: rgba(0, 0, 0, 0.8);
	}
	.model-show {
		display: flex;
    justify-content: center;
    align-items: center;
		width: 100vw;
		height: 100vh;
	}
	.model img {
		display: block;
		margin: auto;
		max-width: 80%;
		user-select: none;
		background-position: 0px 0px, 10px 10px;
		background-size: 20px 20px;
    background-image: linear-gradient(45deg, #eee 25%, transparent 25%, transparent 75%, #eee 75%, #eee 100%),linear-gradient(45deg, #eee 25%, white 25%, white 75%, #eee 75%, #eee 100%);
	}
	.c-item {
		display: block;
		padding: 10px 0;
		user-select: none;
	}
	@keyframes slide {
    0%  {
      background-position: 0 0;
    }
    100% {
      background-position: -100% 0;
    }
  }
</style>


