<template>
  <div class="dayList">
    <mOFheader :title="id"></mOFheader>
    <ul v-if="list.length>0">
      <li v-for="(item,index) in list[0].datalist" :key="index">
        <p>
          <span>{{item.book_time}}</span>
          <span>分派护士：{{item.nurse_name}} </span>
        </p>
        <h3>{{item.product_name}}<span>{{item.order_text}}</span></h3>
        <h4>{{item.order_address}}</h4>
      </li>
    </ul>
  </div>
</template>

<script>
import { getVisit } from '../service/getData'
  import mOFheader from '../components/mOFheader.vue'
  export default {
    name: 'dayList',
    components: {
      mOFheader
    },
    data() {
      return {
        date: '',
        id: '',
        list: []
      }
    },
    created() {
      this.id = this.$route.params.id
    },
    mounted() {
      this.getData()
    },
    methods: {
      getData() {
        let data = {
          token: this.$store.state.userId,
          begin_time: this.id,
          end_time: this.id
        }
        getVisit(data).then(res => {
          this.list = res.data.list
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/dayList.scss';
</style>



