<template>
  <div class="message">
    <div class="title">
      <img @click="back" src="../assets/images/header/back.png" alt="">
      <h2>消息</h2>
      <span @click="read" :class="waitRead === true ? '':'waitRead'">全部已读</span>
    </div>

    <div class="list-item" :key="index" v-for="(item, index) in MsgList">
      <h3>{{index}}</h3>
      <ul>
      <van-cell-swipe :right-width="rightWidth" :key="i" v-for="(one, i) in item">
        <van-cell-group>
          <li @click="clickMsg(index, i)">
            <i :class="one.remind_flag === 0? '' : 'none'"></i>
            <section>
              <h5>{{one.sms_title}}</h5>
              <h4>{{one.sms_content}}</h4>
            </section>
          </li>
        </van-cell-group>
        <span slot="right" id="right" @click="deleteMsg($event, one.id)">删除</span>
      </van-cell-swipe>
      </ul>
    </div>

    <section class="noMessage" v-if="MsgList === ''">
      <span>暂时没有新消息</span>
    </section>
  </div>
</template>

<script>
  import {
    getMsgs,
    delMsgs,
    changeStatus
  } from '../service/getData'
  export default {
    name: 'message',
    data() {
      return {
        rightWidth: 90,
        MsgList: '',
        waitRead: false
      }
    },
    mounted: function() {
      this.getMsgList(1);
    },
    methods: {
      back: function() {
        this.$router.go(-1)
      },
      read: function() {
        var ids = [];
        for (var key in this.MsgList) {
          for (var key2 in this.MsgList[key]) {
            if (this.MsgList[key][key2].remind_flag === 0) {
              ids.push(this.MsgList[key][key2].id)
            }
          }
        }
        changeStatus(this.$store.state.userId, ids).then(res => {
          if (res.msg === "获取成功") {
            for (var key in this.MsgList) {
              for (var key2 in this.MsgList[key]) {
                this.MsgList[key][key2].remind_flag = 1
              }
            }
            this.waitRead = false
          }
        })
      },
      getMsgList: function(page) {
        getMsgs(this.$store.state.userId, page).then(res => {
          if (res.data.length !== 0) {
            this.MsgList = res.data
            this.more()
          }
        })
      },
      more() {
        this.waitRead = false
        for (let day in this.MsgList) {
          for (let msg in this.MsgList[day]) {
            if (this.MsgList[day][msg].remind_flag === 0) {
              this.waitRead = true
            }
          }
        }
      },
      deleteMsg: function(e, id) {
        var ids = [];
        ids.push(id);
        delMsgs(this.$store.state.userId, ids).then(res => {
          if (res.msg === "删除成功") {
            e.target.parentNode.parentNode.parentNode.style.display = 'none'
          }
        })
      },
      clickMsg(index, msg) {
        this.MsgList[index][msg].remind_flag = 1
        var ids = [];
        ids.push(this.MsgList[index][msg].id)
        changeStatus(this.$store.state.userId, ids).then(res => {
          if (res.msg === "获取成功") {
            this.more()
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/message.scss';
</style>



