<template>
  <div class="details">
    <div class="flex">
      <div>
        <calendar :events="calendar.events" :range="calendar.range" :lunar="calendar.lunar" :value="calendar.value" :begin="calendar.begin" :end="calendar.end" @select="calendar.select" @goback="goback" @selectMonth="selectMonth"></calendar>
      </div>
    </div>
  </div>
</template>

<script>
import calendar from '@/components/calendar/calendar.vue'
import { weekList } from '../service/getData'
import moment from 'moment'
export default {
  name: 'month',
  components: {
    calendar
  },
  data() {
    return {
      date: '',
      tdText: [],
      begin_time: '',
      end_time: '',
      calendar: {
        range: true,
        lunar: false, //显示农历
        begin: [2017, 1, 1], //可选开始日期
        end: [2020, 1, 1], //可选结束日期
         events: [],
        select(value) {
          // console.log(value.toString())
        }
      }
    }
  },
  created() {
    this.begin_time = moment()
      .startOf('month')
      .format('YYYY-MM-DD')
    this.end_time = moment()
      .endOf('month')
      .format('YYYY-MM-DD')
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      let data = {
        token: this.$store.state.userId,
        begin_time: this.begin_time,
        end_time: this.end_time
      }
      weekList(data).then(res => {
        this.list = res.data.list
        this.events = res.data.list
        this.indexday = 0
      })
    },
    selectMonth(month, year) {
      let data = moment([year, month-1]).format('YYYY-MM-DD')
      this.begin_time = moment(data).startOf('month').format('YYYY-MM-DD')
      this.end_time = moment(data).endOf('month').format('YYYY-MM-DD')
      this.getData()
    },
    goback(date) {
      this.$router.push("/outCallList/"+moment(date).format('YYYY-MM-DD'))
    }
  }
}
</script>

<style lang="scss" scoped>
/*demo*/
.flex {
  box-sizing: border-box;
  display: -webkit-box;
  -webkit-box-pack: start;
  -webkit-box-align: start;
  display: -webkit-flex;
  -webkit-justify-content: space-between;
  -webkit-align-items: top;
  display: flex;
  justify-content: space-between;
  align-items: top;
  flex-flow: row wrap;
}
.flex > div {
  margin: 10px;
  padding: 20px;
  width: 100%;
  min-width: 600px;
  border-radius: 2px;
  position: relative;
}
.flex > div > span {
  position: absolute;
  left: 0px;
  top: 0px;
  padding: 5px 10px;
  font-family: 'PingFang SC', 'Hiragino Sans GB', 'STHeiti', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  font-size: 10px;
  border-radius: 0 0 2px 0;
  background: #ea6151;
  color: #fff;
}
.flex > div > input {
  box-sizing: border-box;
  width: 100%;
  margin-top: 20px;
  border-radius: 2px;
  border: 1px solid #dedede;
  padding: 10px;
  font-size: 16px;
  background: url(data:image/svg+xml;base64,)
    no-repeat 8px 10px;
  padding-left: 36px;
  color: #666;
}
/*transition*/
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease-in-out;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
  transform: translateY(-10px);
}
/*下拉框*/
.calendar-dropdown {
  background: #fff;
  position: absolute;
  left: 0;
  top: 0;
  padding: 20px;
  border: 1px solid #eee;
  border-radius: 2px;
}
.calendar-dropdown:before {
  position: absolute;
  left: 30px;
  top: -10px;
  content: '';
  border: 5px solid rgba(0, 0, 0, 0);
  border-bottom-color: #dedede;
}
.calendar-dropdown:after {
  position: absolute;
  left: 30px;
  top: -9px;
  content: '';
  border: 5px solid rgba(0, 0, 0, 0);
  border-bottom-color: #fff;
}
/*弹出框*/
.calendar-dialog {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.calendar-dialog-mask {
  background: rgba(255, 255, 255, 0.5);
  width: 100%;
  height: 100%;
}
.calendar-dialog-body {
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  border: 1px solid #eee;
  border-radius: 2px;
}
</style>



