<template>
  <div class="toBeSetOut">
    <mOFheader title="订单详情"></mOFheader>
    <div class="logoBox">
      <img src="../assets/images/DDdetail/ddcf.png" alt="">
      <div class="banner">
        <p><img src="../assets/images/myOrderForm/ru.png" alt=""><span>{{main.product.product_name}}</span></p>
        <div class="label">
          <span>产妇护理包</span>
          <span>护理员职业责任保险</span>
        </div>
      </div>
    </div>
    <div class="user">
      <section>
        <p><i></i><span>患者姓名</span></p>
        <p>{{main.order_patient.patient_name}}</p>
      </section>
      <section>
        <p><i></i><span>患者年龄</span></p>
        <p>{{main.order_patient.patient_age}}岁</p>
      </section>
      <section>
        <p><i></i><span>患者性别</span></p>
        <p>{{main.order_patient.patient_gender}}</p>
      </section>
      <section>
        <p><i></i><span>是否本院患者</span></p>
        <p v-if="main.order_patient.is_hospital === 1">是</p>
        <p v-else>否</p>
      </section>
    </div>
    <div class="address">
      <section>
        <span>服务时间</span>
        <p>{{main.book_time_text}}</p>
      </section>
      <section>
        <span>服务地点</span>
        <p>江苏省南京市江宁区将军大道托乐嘉睦领居12栋3 03室</p>
      </section>
      <section>
        <span>病情详情</span>
        <p>需要多次催乳</p>
      </section>
    </div>
    <div class="btn">
      <a href="#" class="btn2"><img src="../assets/images/DDdetail/tel.png" alt="">联系患者</a>
    </div>
  </div>
</template>

<script>
  import mOFheader from '../components/mOFheader.vue'
  export default {
    name: 'toBeSetOut',
    components: {
      mOFheader
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/toBeSetOut.scss';
</style>



