<template>
  <div class="center">
    <div class="bg">
      <div class="title">
        <img @click="back" :src="require('../assets/images/header/back.png')" alt="">
        <img @click="set" :src="require('../assets/images/center/set.png')" alt="">
      </div>
      <img class="header" :src="userMsg.head_pic" alt="">
      <p><span :class="userMsg.gender === 1 ? 'boy' : 'girl'">{{userMsg.realname}}</span></p>
      <p>
        <i v-if="userMsg.zhicheng === 1">护士长</i>
        <i v-if="userMsg.zhicheng === 2">护士</i>
        <i v-if="userMsg.zhicheng === 3">护师</i>
        <i>工作{{userMsg.work_years}}年</i>
      </p>
      <h3>{{userMsg.hospital_name}}-{{userMsg.keshi_name}}</h3>
      <h4>{{userMsg.info}}</h4>
      <section>
        <div class="">
          <span>{{userMsg.orders_count}}</span>
          <i>接单数</i>
        </div>
        <div class="">
          <span>{{userMsg.jifen}}</span>
          <i>积分</i>
        </div>
      </section>
    </div>
    <div @click="outCallWeek" class="czjl">
      <span>出诊记录</span>
    </div>
    <div @click="orderHistoryWeek" class="wddd">
      <span>我的订单</span>
    </div>
  </div>
</template>

<script>
  import {
    nurseCenter
  } from '../service/getData'
  export default {
    name: 'center',
    data(){
      return{
        userMsg: ''
      }
    },
    mounted: function() {
      nurseCenter(this.$store.state.userId).then(res => {
        this.userMsg = res.data
        if(this.userMsg.head_pic == ''){
          this.userMsg.head_pic = require('../assets/images/default/default-center180x180.png');
        }
      })
    },
    methods: {
      back: function() {
        this.$router.go(-1)
      },
      set: function() {
        this.$router.push({
          name: 'set'
        })
      },
      outCallWeek: function() {
        this.$router.push({
          name: 'outCallWeek'
        })
      },
      orderHistoryWeek: function() {
        this.$router.push({
          name: 'myOrderForm',
          params: {
            status: '全部'
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/center.scss';
</style>



